import React, { useState, useMemo } from 'react'
import { caseName1, caseName2, caseRole, lawyer } from '../../../../Datas/datas';
import {
    parseOnlyNumber
} from '../../../utils/unitParse';
import Modal from '../../../utils/Modal';
import ConsultHistory from '../Sections/ConsultHistory.js';

function SangdamPart(props) {
    const [modal, setModal] = useState({ status: false, text: '' }); //  state
    const [callConsultContent, setCallConsultContent] = useState(''); //   state
    const [visitConsultContent, setVisitConsultContent] = useState(''); //   state
    const [callConsultResult, setCallConsultResult] = useState(''); //   
    const [visitConultResult, setVisitConultResult] = useState(''); //   

    const [standardSangdam, setStandardSangdam] = useState({  //   state
        caseBig: '',
        caseMiddle1: '',
        caseMiddle2: '',
        caseRole1: '',
        caseRole2: '',
        consultManager1: '',
        consultManager2: '',
        consultLawyer1: '',
        consultLawyer2: '',
        consultLawyer3: '',
        userAge: '',
        userJob: '',
        consultTime1: '',
        consultTime2: '',
        songmuManager: ''
    });

    const { 
        caseBig,
        caseMiddle1,
        caseMiddle2,
        caseRole1,
        caseRole2,
        consultManager1,
        consultManager2,
        consultLawyer1,
        consultLawyer2,
        consultLawyer3,
        userAge,
        userJob,
        consultTime1,
        consultTime2,
        songmuManager
    } = standardSangdam;

    const inputHandler1 = (e) => { //    
        let {name, value} = e.target;
        if (name == 'userAge') value = parseOnlyNumber(value);

        setStandardSangdam({
            ...standardSangdam,
            [name]: value
        });
    };

    const checkRequired = (classNames) => { //   
        let checkEl = document.querySelectorAll(`.${classNames}`);
        for (let i = 0; i < checkEl.length; i++) {
            if (checkEl[i].value === "" && !checkEl[i].disabled) {
                let copy = {...modal};
                copy.status = true;
                copy.text = `${checkEl[i].getAttribute('data-required')} .`
                setModal(copy);
                return false;
            }
        }
        return true;
    }

    const submitResults = (inputClassNames) => { 
        //  , ,  submit 
        if ( checkRequired(inputClassNames) ) {
            if (inputClassNames === 'consult-standard-required') {
                console.log('  ', standardSangdam);
            } else if (inputClassNames === 'results1') {
                let dataToSubmit = Object.assign(standardSangdam, { callConsultResult: callConsultResult } );
                console.log('  ', dataToSubmit);
            } else if (inputClassNames === 'results2') {
                let dataToSubmit = Object.assign(standardSangdam, { visitConultResult: visitConultResult } );
                console.log('  ', dataToSubmit);
            }
        }
    };

    const consultCommentHandler = (type) => { // / submit 
        let targetValue = '';
        if (type === 'call') targetValue = callConsultContent;
        else if (type === 'visit') targetValue = visitConsultContent;
        if (targetValue.trim() === "" ) {
            let copy = {...modal};
            copy.status = true;
            copy.text = ' .';
            setModal(copy);
            return false;
        }
        console.log('DB  ', targetValue);
    }

    const closeModal = () => { setModal(false); }; //   

    const redColor = useMemo(() => ({color: '#f30'}), []);
    const textAreaStyle = useMemo(() => ({ width: '90%', verticalAlign: '-12px', height: '120px' }), []);
    
    return (
        <div className="common-part mb-20 mt-20">
            <section className="part-title clearfix">
                <p className="title-txt"></p>
                <p className="btn-wrapper">
                <span 
                    className={ `default-btn-theme ml-5 ${props.PartShowAndHide.sangdam ? 'minus' : 'plus'}` }
                    onClick={()=>{props.partToggle('sangdam')}}>
                </span>
                </p>
            </section>
            
            {
                props.PartShowAndHide.sangdam
                &&
                <section className="part-content">
                    {/* standard-sangdam-info-table */}
                    <table className="table standard-sangdam-info-table mb-30">
                        <tr>
                            <th>()</th>
                            <td>
                                <select className="common-input w-100p" name="caseBig" value={caseBig} onChange={inputHandler1}>
                                    {caseName1.map((el,idx)=> (<option value={el}>{el}</option>))}
                                </select>
                            </td>
                            <th>()</th>
                            <td>
                                <select className="common-input w-100p mb-5" name="caseMiddle1" value={caseMiddle1} onChange={inputHandler1}>
                                    <option value="" disabled></option>
                                    {caseName2.map((el,idx)=> (<option value={el}>{el}</option>))}
                                </select>
                                <br />
                                <select className="common-input w-100p" name="caseMiddle2" value={caseMiddle2} onChange={inputHandler1}>
                                    <option value="" disabled></option>
                                    {caseName2.map((el,idx)=> (<option value={el}>{el}</option>))}
                                </select>
                            </td>
                            <th></th>
                            <td>
                                <select className="common-input w-100p mb-5" name="caseRole1" value={caseRole1} onChange={inputHandler1}>
                                    <option value="" disabled></option>
                                    {caseRole.map((el,idx)=> (<option value={el}>{el}</option>))}
                                </select>
                                <br />
                                <select className="common-input w-100p" name="caseRole2" value={caseRole2} onChange={inputHandler1}>
                                    <option value="" disabled></option>
                                    {caseRole.map((el,idx)=> (<option value={el}>{el}</option>))}
                                </select>
                            </td>
                            <th><i className="required"></i></th>
                            <td>
                                <select 
                                className="common-input w-100p consult-standard-required results1 results2" 
                                name="consultManager1" 
                                value={consultManager1} 
                                data-required=""
                                onChange={inputHandler1}>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                </select>
                            </td>
                        </tr>
                        <tr>
                            <th>2</th>
                            <td>
                                <select className="common-input w-100p" name="consultManager2" value={consultManager2} onChange={inputHandler1}>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                </select>
                            </td>
                            <th></th>
                            <td colSpan="5" className='ta-l'>
                                <span className="theme-color">1</span>
                                <select className="common-input ml-5 mr-25 w-150" name="consultLawyer1" value={consultLawyer1} onChange={inputHandler1}>
                                    <option value=""></option>
                                    {lawyer.map((el,idx)=> (<option value={el}>{el}</option>))}
                                </select>

                                <span>2</span>
                                <select className="common-input ml-5 mr-25 w-150" name="consultLawyer2" value={consultLawyer2} onChange={inputHandler1}>
                                    <option value=""></option>
                                    {lawyer.map((el,idx)=> (<option value={el}>{el}</option>))}
                                </select>

                                <span>3</span>
                                <select className="common-input ml-5 w-150" name="consultLawyer3" value={consultLawyer3} onChange={inputHandler1}>
                                    <option value=""></option>
                                    {lawyer.map((el,idx)=> (<option value={el}>{el}</option>))}
                                </select>
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td><input type="text" className="common-input w-100p" name="userAge" value={userAge} onChange={inputHandler1}/></td>
                            <th></th>
                            <td>
                                <select className="common-input w-100p" name="userJob" value={userJob} onChange={inputHandler1}>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                </select>
                            </td>
                            <th></th>
                            <td>
                                <p className='ta-l'> <span style={redColor}>{consultTime1}</span></p>
                                <select className="common-input w-100p mt-5" name="consultTime2" value={consultTime2} onChange={inputHandler1}>
                                    <option value=""></option>
                                    <option value="15">15</option>
                                    <option value="30">30</option>
                                    <option value="45">45</option>
                                    <option value="60">60</option>
                                    <option value="60 ">60 </option>
                                </select>
                            </td>
                            <th></th>
                            <td>
                                <select className="common-input w-100p" name="songmuManager" value={songmuManager} onChange={inputHandler1}>
                                    <option value=""></option>
                                </select>
                            </td>
                        </tr>
                        <tr>
                            <td colSpan='8'>
                                <button
                                    onClick={()=>{submitResults('consult-standard-required')}}
                                    className="default-btn-blue big-btn mt-5 mb-5">  
                                </button>
                            </td>
                        </tr>
                    </table>
                
                    {/* //standard-sangdam-info-table */}

                    {/* detail-sangdam-info-table */}
                    <table className="table detail-sangdam-info-table">
                        {/*   */}
                        <tr>
                            <th rowSpan="2" className="pt-20 pb-20">
                                <p><i className="required"></i> </p>
                                <select
                                    data-required=" " 
                                    className="common-input w-100p mt-5 mb-5 results1" 
                                    value={callConsultResult} 
                                    onChange={(e)=>{setCallConsultResult(e.target.value)}}>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                </select>
                                <button 
                                    className="default-btn-blue big-btn"
                                    onClick={()=>{submitResults('results1')}}
                                ></button>
                            </th>
                            <td colSpan="8" className="p-10">
                                <ConsultHistory/>
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td colSpan="7" className="p-10">
                                <textarea 
                                    className="common-input"
                                    style={textAreaStyle}
                                    value={callConsultContent}
                                    onChange={(e)=>{setCallConsultContent(e.target.value)}}
                                ></textarea>
                                <button 
                                    className="default-btn-green big-btn ml-5"
                                    onClick={()=>{consultCommentHandler('call')}}  
                                ></button>
                            </td>
                        </tr>
                        {/* //   */}

                        {/*   */}
                        <tr>
                            <th rowSpan="2" className="pt-20 pb-20">
                                <p><i className="required"></i> </p>
                                <select
                                    data-required=" " 
                                    className="common-input w-100p mt-5 mb-5 results2" 
                                    value={visitConultResult} 
                                    onChange={(e)=>{setVisitConultResult(e.target.value)}}>
                                    <option value=""></option>
                                        <option value=" "> </option>
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                    </select>
                                <button 
                                    className="default-btn-blue big-btn"
                                    onClick={()=>{submitResults('results2')}}    
                                ></button>
                            </th>
                            <td colSpan="8" className="p-10">
                                <ConsultHistory />
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td colSpan="7" className="p-10">
                                <textarea
                                    className="common-input" 
                                    style={textAreaStyle}
                                    value={visitConsultContent}
                                    onChange={(e)=>{setVisitConsultContent(e.target.value)}}
                                 ></textarea>
                                <button 
                                    className="default-btn-green big-btn ml-5"
                                    onClick={()=>{consultCommentHandler('visit')}}
                                ></button>
                            </td>
                        </tr>
                        {/* //   */}

                    </table>
                    {/* //detail-sangdam-info-table */}
                </section>
            }
            {modal.status && <Modal refreshFunction={closeModal} modalText={modal.text}/>}
        
        </div>
    )
}

export default SangdamPart
