import React, { useState } from "react";
import { useLocation } from "react-router-dom";
import './Sections/Navigation.css';
import { Link } from 'react-router-dom';

// fontawesome Icon
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faPen,
  faHeadset,
  faEye,
  faHandshake,
  faWonSign,
  faUsers,
  faEdit,
  faCommentsDollar,
  faCalculator,
  faChartBar,
  faCog,
} from "@fortawesome/free-solid-svg-icons";

function Navigation() {
  const { pathname } = useLocation();

  const [MenuUrlLists, setMenuUrlLists] = useState([
    { urlLists: ['/receipt', '/mainUser'] },
    { urlLists: ['/consultation'] }, 
    { urlLists: ['/visitList1', '/visitList2'] },
    { urlLists: ['/contract'] },
    { urlLists: ['/sales', '/salesDetail', '/notpayList', '/refundList', '/contractChangeList'] },
    { urlLists: ['/baedangList'] },
    { urlLists: ['/caseList'] },
    { urlLists: [''] },
    { urlLists: ['h'] },
    { urlLists: ['/scheduler', '/allStatistics', '/statisticsByManager', '/inflowChart'] },
    { urlLists: ['/pageOption', '/memberList', '/authSetting', '/cardSetting', '/caseSetting', '/caseCodeSetting', '/callCheck', '/smsSend'] }
  ]);

  const pageNotYet = (e) => {
    e.preventDefault();
    alert('');
  }

  return (
    <div className="navi-wrap">
      <div className="page-container clearfix">
          <article className={MenuUrlLists[0].urlLists.indexOf(pathname) > -1 && 'active'}>
            <Link className="navi-list" to='/receipt'>
              <FontAwesomeIcon icon={faPen} className="navigation-ico" />
              <p></p>
            </Link>
          </article>

          <article className={MenuUrlLists[1].urlLists.indexOf(pathname) > -1 && 'active'}>
            <Link className="navi-list" to='/consultation'>
              <FontAwesomeIcon icon={faHeadset} className="navigation-ico" />
              <p></p>
            </Link>
          </article>

          <article className={MenuUrlLists[2].urlLists.indexOf(pathname) > -1 && 'active'}>
            <a className="navi-list">
              <FontAwesomeIcon icon={faEye} className="navigation-ico" />
              <p></p>
              <ul className="sub-menu-list balloon">
                <li><Link to="/visitList1"></Link></li>
                <li><Link to="/visitList2"></Link></li>
              </ul>
            </a>
          </article>

          <article className={MenuUrlLists[3].urlLists.indexOf(pathname) > -1 && 'active'}>
            <a className="navi-list" >
              <FontAwesomeIcon icon={faHandshake} className="navigation-ico" />
              <p></p>
              <ul className="sub-menu-list balloon">
                <li><Link to="/contract"></Link></li>
                <li><Link onClick={pageNotYet}></Link></li>
              </ul>
            </a>
          </article>

          <article className={MenuUrlLists[4].urlLists.indexOf(pathname) > -1 && 'active'}>
            <a className="navi-list">
              <FontAwesomeIcon icon={faWonSign} className="navigation-ico" />
              <p></p>
              
              <ul className="sub-menu-list balloon">
                <li><Link to="/sales"></Link></li>
                <li><Link to="/salesDetail"></Link></li>
                <li><Link to="/notpayList"></Link></li>
                <li><Link to="/refundList"></Link></li>
                <li><Link to="/contractChangeList"></Link></li>
              </ul>
            </a>
          </article>

          <article className={MenuUrlLists[5].urlLists.indexOf(pathname) > -1 && 'active'}>
            <Link className="navi-list" to='/baedangList'>
              <FontAwesomeIcon icon={faUsers} className="navigation-ico" />
              <p></p>
            </Link>
          </article>

          <article className={MenuUrlLists[6].urlLists.indexOf(pathname) > -1 && 'active'}>
            <Link className="navi-list" to='/caseList'>
              <FontAwesomeIcon icon={faEdit} className="navigation-ico" />
              <p></p>
            </Link>
          </article>

          <article className={MenuUrlLists[7].urlLists.indexOf(pathname) > -1 && 'active'}>
            <Link className="navi-list" onClick={pageNotYet}>
              <FontAwesomeIcon icon={faCommentsDollar} className="navigation-ico" />
              <p></p>
            </Link>
          </article>

          <article className={MenuUrlLists[8].urlLists.indexOf(pathname) > -1 && 'active'}>
            <Link className="navi-list" onClick={pageNotYet}>
              <FontAwesomeIcon icon={faCalculator} className="navigation-ico" />
              <p></p>
            </Link>
          </article>

          <article className={MenuUrlLists[9].urlLists.indexOf(pathname) > -1 && 'active'}>
            <a className="navi-list">
              <FontAwesomeIcon icon={faChartBar} className="navigation-ico" />
              <p></p>

              <ul className="sub-menu-list balloon">
                <li><Link to="/scheduler"></Link></li>
                <li><Link to="/" onClick={pageNotYet}>CAPA</Link></li>
                <li className="depts-3-wrap">
                  <a> </a>
                  {/* <ul className="depts-3-lists balloon">
                    <li><Link to="/">CP</Link></li>
                    <li><Link to="/"></Link></li>
                    <li><Link to="/"></Link></li>
                    <li><Link to="/">/</Link></li>
                    <li><Link to="/"></Link></li>
                    <li><Link to="/"></Link></li>
                    <li><Link to="/"></Link></li>
                    <li><Link to="/">  </Link></li>
                    <li><Link to="/"></Link></li>
                  </ul> */}
                </li>
                <li className="depts-3-wrap">
                  <a> (i-boss)</a>
                  <ul className="depts-3-lists balloon">
                    <li><Link to="/allStatistics"></Link></li>
                    <li><Link to="/statisticsByManager"> </Link></li>
                    <li><Link to="/inflowChart"></Link></li>
                  </ul>
                </li>
              </ul>
            </a>
          </article>

          <article className={MenuUrlLists[10].urlLists.indexOf(pathname) > -1 && 'active'}>
            <a className="navi-list">
              <FontAwesomeIcon icon={faCog} className="navigation-ico" />
              <p></p>

              <ul className="sub-menu-list balloon">
                <li><Link to="/memberList" ></Link></li>
                <li><Link to="/authSetting">/ </Link></li>
                <li><Link to="/cardSetting"></Link></li>
                <li><Link to="/caseSetting"></Link></li>
                <li><Link to="/caseCodeSetting"></Link></li>
                <li><Link to="/callCheck"></Link></li>
                <li><Link to="/smsSend"></Link></li>
                <li><Link to="/" onClick={pageNotYet}>-</Link></li>
                <li><Link to="/" onClick={pageNotYet}>- </Link></li>
                <li><Link to="/pageOption">  </Link></li>
              </ul>
            </a>
          </article>
      </div>
    </div>
  );
}

export default Navigation;
