import React, { useState } from 'react';
import { caseRole, caseName2 } from '../../../../Datas/datas';
import Modal from '../../../utils/Modal';
import { Link } from 'react-router-dom';

function LegalReportTable() {
    const [modal, setModal] = useState({ status: false, text: '' }); //  state
    const closeModal = () => { setModal(false); }; //   
    
    const [legalReportInfo, setLegalReportInfo] = useState({ //   state
        caseMiddle: '', // ()
        caseDetail: '', // ()
        role: '', // 
        speedLevel: '', // 
        changeLevel: '', // 
        difficultLevel: '', // 
        volumeLevel: '', // 
        totalComment: '', // 
        totalFact: '', // 
        totalFact2: '', // ()  
        totalLawyer: '', //  
        totalWant: '', //  
        totalData1: '', //  
        totalData2: '', //   
    }); 

    const {
        caseMiddle,
        caseDetail,
        role,
        speedLevel,
        changeLevel,
        difficultLevel,
        volumeLevel,
        totalComment,
        totalFact,
        totalFact2,
        totalLawyer,
        totalWant,
        totalData1,
        totalData2
    } = legalReportInfo;

    const inputHandler = (e) => { 
        let {name, value} = e.target;

        setLegalReportInfo({
            ...legalReportInfo,
            [name]: value
        })
    };

    const checkRequired = (classNames) => { //   
        let checkEl = document.querySelectorAll(`.${classNames}`);
        for (let i = 0; i < checkEl.length; i++) {
            if (checkEl[i].value === "" && !checkEl[i].disabled) {
                let copy = {...modal};
                copy.status = true;
                copy.text = `${checkEl[i].getAttribute('data-required')} .`
                setModal(copy);
                return false;
            }
        }
        return true;
    };

    const submitHandler = () => {
        if ( checkRequired('caseDetail') ) {
            console.log(' ', legalReportInfo);
        }  
    }
    
    return (
        <>
            <table className='table legal-report-info-table'>
            <tr>
                <th rowSpan='10'>
                    <p> </p>    
                    <button 
                    onClick={submitHandler}
                    className="default-btn-blue big-btn mt-20 mb-20"></button><br/>
                    <Link target="_blank" to={`/reportLegalReview/123`} rel="noopener noreferrer" className="default-btn-theme mb-5 w-125" > </Link>
                </th>
                <th>()</th>
                <td colSpan="2">
                    <select 
                    name="caseMiddle"
                    value={caseMiddle}
                    onChange={inputHandler}
                    className="common-input w-100p">
                        <option value=""></option>
                        {caseName2.map((el,idx)=>(<option key={idx} value={el}>{el}</option>))}
                    </select>
                </td>
                <th><i className="required"></i>()</th>
                <td colSpan="3">
                    <input type="text" 
                    name="caseDetail"
                    value={caseDetail}
                    onChange={inputHandler}
                    data-required="()"
                    className="common-input w-100p caseDetail"/>
                </td>
                <th></th>
                <td colSpan="2">
                    <select 
                    name="role"
                    value={role}
                    onChange={inputHandler}
                    className="common-input w-100p">
                        <option value=""></option>
                        {caseRole.map((el,idx)=>(<option key={idx} value={el}>{el}</option>))}
                    </select>
                </td>
            </tr>
            <tr>
                <th></th>
                <td>
                    <select 
                    name="speedLevel"
                    value={speedLevel}
                    onChange={inputHandler}
                    className="common-input w-100p">
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                    </select>
                </td>
                <th></th>
                <td>
                    <select 
                    name="changeLevel"
                    value={changeLevel}
                    onChange={inputHandler}
                    className="common-input w-100p">
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                    </select>
                </td>
                <th></th>
                <td>
                    <select 
                    name="difficultLevel"
                    value={difficultLevel}
                    onChange={inputHandler}
                    className="common-input w-100p">
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                    </select>
                </td>
                <th></th>
                <td>
                    <select 
                        name="volumeLevel"
                        value={volumeLevel}
                        onChange={inputHandler}
                        className="common-input w-100p">
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                    </select>
                </td>
                <td colSpan='2'></td>
            </tr>
            <tr>
                <th></th>
                <td colSpan="9">
                    <input type="text" 
                    name="totalComment"
                    value={totalComment}
                    onChange={inputHandler}
                    className="common-input w-100p"/>
                </td>
            </tr>
            <tr>
                <th></th>
                <td colSpan="9"><textarea 
                name="totalFact"
                value={totalFact}
                onChange={inputHandler}
                className='common-input w-100p h-120'></textarea></td>
            </tr>
            <tr>
                <th>()  </th>
                <td colSpan="9"><textarea 
                name="totalFact2"
                value={totalFact2}
                onChange={inputHandler}
                className='common-input w-100p h-120'></textarea></td>
            </tr>
            <tr>
                <th> </th>
                <td colSpan="9"><textarea 
                name="totalLawyer"
                value={totalLawyer}
                onChange={inputHandler}
                className='common-input w-100p h-120'></textarea></td>
            </tr>
            <tr>
                <th> </th>
                <td colSpan="9"><textarea 
                name="totalWant"
                value={totalWant}
                onChange={inputHandler}
                className='common-input w-100p h-120'></textarea></td>
            </tr>
            <tr>
                <th> </th>
                <td colSpan="9"><textarea 
                name="totalData1"
                value={totalData1}
                onChange={inputHandler}
                className='common-input w-100p'></textarea></td>
            </tr>
            <tr>
                <th>  </th>
                <td colSpan="9"><textarea 
                name="totalData2"
                value={totalData2}
                onChange={inputHandler}
                className='common-input w-100p'></textarea></td>
            </tr>
        </table>
        {modal.status && <Modal refreshFunction={closeModal} modalText={modal.text}/>}
        </>
    )
}

export default LegalReportTable
