import React, { useState } from "react";
import SearchBox from "../../utils/SearchBox";
import articleLists from '../../../Datas/articleLists1';
import HoverText from '../../utils/HoverText';
import Paging from '../../utils/Paging';
import { Link } from 'react-router-dom';

function ReceiptListPage() {
  const [Article, setArticle] = useState(articleLists)
  const [CheckBox, setCheckBox] = useState([]);
  const [HoverTextContent, setHoverTextContent] = useState('');
  const [MousePosition, setMousePosition] = useState({x: '', y: ''});

  const checkboxToggle = (value) => {
    const currentIdx = CheckBox.indexOf(value);
    const copy = [...CheckBox];
    if ( currentIdx === -1 ) {
      copy.push(value);
    } else {
      copy.splice(currentIdx, 1);
    }
    setCheckBox(copy);
  }
  const checkAll = (e) => {
    const isChecked = e.target.checked;
    if (isChecked) {
      const AllCheckLists = Array.from({length: articleLists.length}, (v, i) => i); 
      setCheckBox(AllCheckLists);
    } else {
      setCheckBox([]);
    }
  }

  const mouseMoveHandler = (value, e) => {
    setHoverTextContent(value);
    setMousePosition({ x: e.clientX, y: e.clientY });
  }

  return (
    <div className="page-container">
      <SearchBox option={{
        dateStart: true,
        dateEnd: true,
        route: true,
        isNew: true,
        consultResult1: true,
        consultResult2: true,
        jeopsuEmployee: true,
        searchText: true
      }}/>

      <div className="table-title clearfix">
        <p className="title-txt"></p>

        <a href="javascript:;" className="default-btn"></a>
        <a href="javascript:;" className="default-btn-blue"></a>
        <a href="javascript:;" className="default-btn-red"></a>
        <a href="javascript:;" className="default-btn-theme"></a>
      </div>

      <div className="table_wrapper">
        <table className="table">
          <thead>
            <tr>
              <th className="w-40"><input type="checkbox" onChange={checkAll}/></th>
              <th className="w-100"></th>
              <th className="w-82"></th>
              <th className="w-70"></th>
              <th className="w-120"></th>
              <th className="w-130"></th>
              <th className="w-120">()</th>
              <th className="w-70"></th>
              <th className="w-50"></th>
              <th className="w-310"></th>
              <th className="w-230"></th>
              <th className="w-110">1</th>
              <th className="w-50">CP </th>
              <th className="w-100"></th>
            </tr>
          </thead>
          <tbody>
            {
                Article.map((el,idx)=>(
                    <tr key={idx}>
                        <td><input type="checkbox" onChange={()=>checkboxToggle(el._id)} checked={CheckBox.indexOf(el._id) === -1 ? '' : 'checked'}/></td>
                        <td>{el.route}</td>
                        <td>{el.date}<br/>[{el.time}]</td>
                        <td>{el.isNew}</td>
                        <td><Link to="/mainUser">{el.name}</Link></td>
                        <td>{el.phone}</td>
                        <td>{el.caseName1}</td>
                        <td>{el.area}</td>
                        <td>{el.gender}</td>
                        <td
                          className='ta-l'
                          onMouseEnter={(e)=>{mouseMoveHandler(el.jeopsuCon, e)}} 
                          onMouseMove={(e)=>{mouseMoveHandler(el.jeopsuCon, e)}}
                          onMouseLeave={()=>setHoverTextContent('')} 
                        >
                          <span className='ellipsis-2'>{el.jeopsuCon}</span>
                        </td>
                        <td>{el.etcCon}</td>
                        <td>{el.consulResults}</td>
                        <td>{el.cp}</td>
                        <td>{el.employee}</td>
                    </tr>
                ))
            }
          </tbody>
        </table>
      </div>
      {HoverTextContent != "" && <HoverText HoverTextContent={HoverTextContent} MousePosition={MousePosition}/>}

      <Paging/>
    </div>
  );
}

export default ReceiptListPage;
