import React, { useState } from "react";
import SearchBox from "../../utils/SearchBox";
import articleLists from '../../../Datas/articleLists4';

function ContractListPage() {
  const [Article, setArticle] = useState(articleLists);

  return (
    <div className="page-container">
      <SearchBox option={{
        dateStart: true,
        dateEnd: true,
        route: true,
        isNew: true,
        consultResult1: true,
        consultResult2: true,
        jeopsuEmployee: true,
        searchText: true
      }}/>

      <div className="table-title clearfix">
        <p className="title-txt"></p>

        <a href="javascript:;" className="default-btn"></a>
      </div>

      <div className="table_wrapper">
        <table className="table">
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th>()</th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>

       
            {
                Article.map((el,idx)=>(
                    <tr key={idx}>
                      <td>{el.visitConsultationResult}</td>
                      <td>{el.contractDate}</td>
                      <td>{el.isNew}</td>
                      <td>{el.name}</td>
                      <td>{el.phone}</td>
                      <td>{el.caseName2}</td>
                      <td>{el.caseRole}</td>
                      <td>{el.contractMoney}</td>
                      <td><span className="default-btn-theme"></span></td>
                      <td><span className="default-btn-theme"></span></td>
                      <td><span className="default-btn-theme"></span></td>
                      <td>{el.consultManager}</td>
                      <td>{el.consultLawyer1}</td>
                    </tr>
                ))
            }
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default ContractListPage;
