import React, { useState, useEffect, useMemo } from 'react';
import DatepickerCal from '../../../utils/DatepickerCal';
import { employee, lawyer, creditCards } from '../../../../Datas/datas';
import { parseCurrency } from '../../../utils/unitParse';
import Modal from '../../../utils/Modal';
import PayUpdateModal from '../Sections/PayUpdateModal';

function PaymanagePart(props) {
    
    const [modal, setModal] = useState({ status: false, text: '' }); //  state
    const [Paymethod, setPaymethod] = useState(''); //  /  state
    const [payModal, setPayModal] = useState(false); //  modal  state

    const [depositInfo, setDepositInfo] = useState({ //   state
        depositMoney: '',
        depositDate: '',
        howPay: '',
        cards: '',
        depositPaysort: '',
        paper: '',
        issueNum: '',
        depositEtcCon: ''
    }); 

    const {
        depositMoney,
        howPay,
        cards,
        depositPaysort,
        paper,
        issueNum,
        depositEtcCon
    } = depositInfo;

    const [standardPayInfo, setStandardPayInfo] = useState({ //    state
        contractDate: '2021-10-10',
        contractChangeDate: '',
        consultManager: '',
        consultLawyer: '',
        isNew: '',
        userName: props.userName,
        allPay: '',
        changePay: '',
        etcText: ''
    });

    const { 
        contractDate,
        contractChangeDate,
        consultManager,
        consultLawyer,
        isNew,
        userName,
        allPay,
        changePay,
        etcText
    } = standardPayInfo;

    const [DividePayInfo, setDividePayInfo] = useState([]); //    state

    const checkRequired = (classNames) => { //   
        let checkEl = document.querySelectorAll(`.${classNames}`);
        for (let i = 0; i < checkEl.length; i++) {
            if (checkEl[i].value === "" && !checkEl[i].disabled) {
                let copy = {...modal};
                copy.status = true;
                copy.text = `${checkEl[i].getAttribute('data-required')} .`
                setModal(copy);
                return false;
            }
        }
        return true;
    }

    const payDefaultInputHandler = (e) => { //  -   
        let {name, value} = e.target;
        if (name == 'allPay' || name == 'changePay') { value = parseCurrency(value); }
        setStandardPayInfo({
            ...standardPayInfo,
            [name]: value
        })
    };

    const depositHandler = (e) => { //  -  
        let {name, value} = e.target;
        if (name == 'depositMoney') {value = parseCurrency(value);}
        else if (name == 'howPay') { 
            if (value === '') {
                depositInfo.paper = ""; 
                depositInfo.issueNum = ""; 
            } else {
                depositInfo.cards = ""; 
            }
        } 

        setDepositInfo({
            ...depositInfo,
            [name]: value
        })
    };

    const dividePayInfoHandler = (type, index) => { //     / 
        if (type === 'plus') {
            let paysort = DividePayInfo.length > 0 ? DividePayInfo[0].paySort : '';
            setDividePayInfo([...DividePayInfo, {paySort: paysort, payAmount: '', payDate: ''}]);
        } else if (type == 'minus') {
            let copy = [...DividePayInfo];
            copy.splice(index, 1);
            setDividePayInfo(copy);
        }
    };

    const inputChangeHandler = (e, index, target) => { //   - ,   
        let copy = [...DividePayInfo];
        if (target === "paySort") {
            copy.map((el, idx) => {
                el.paySort = e.target.value;
            });
        } else if (target === "payAmount") {
            copy[index].payAmount = parseCurrency(e.target.value);
        }
        setDividePayInfo(copy);
    };

    const payMethodFn = (e) => { //  /   show/hide 
        let payMethodSelection = '';
        if(!e) payMethodSelection = document.getElementById('pay-method-selection').value;
        else payMethodSelection = e.target.value;
        setPaymethod(payMethodSelection);
    };

    const deletePayHistory = () => { //   
        if(window.confirm('?')) {
        }
    }

    const divideDateHandler = (date, index) => {
        let copy = [...DividePayInfo];
        copy[index].payDate = date;
        setDividePayInfo(copy);
    };

    const payDateChange = (date) => { 
        let copy = {...depositInfo};
        copy.depositDate = date;
        setDepositInfo(copy);
    };

    const submitPayDefaultInfo = (types) => {
        if ( checkRequired(types) ) {
            if (types == "pay_required_01") {
                console.log(' ', standardPayInfo);
            } else if (types == "divide_required") {
                console.log(' ', DividePayInfo);
            } else if (types == "allPay_required") {
                console.log(' ', depositInfo);
            }
        }
    };

    const closeModal = () => { setModal(false); }; //   
    
    useEffect(() => {
        payMethodFn();
    }, []);

    const borderTopGrey = useMemo(() => ({borderTop: '1px solid #ccc'}), []);
    const mw_200 = useMemo(() => ({minWidth: '200px'}), []);
    const colorRed = useMemo(() => ({color: '#f30'}), []);
    const widthDetail = useMemo(() => ({width: '261.16px'}), []);
    const greyFont = useMemo(() => ({color: '#999'}), []);
    const widthCalc = useMemo(() => ({width: 'calc(100% - 90px)'}), []);
    const widthCalc2 = useMemo(() => ({width: 'calc(50% - 5px)'}), []);
    const btnStyles = useMemo(() => ({position: 'absolute', right: '10px', top:'6px'}), []);
    
    return (
        <div className="common-part mb-20 mt-20">
            <section className="part-title clearfix">
                <p className="title-txt"></p>
                <p className="btn-wrapper">
                    <span 
                        className={ `default-btn-theme ml-5 ${props.PartShowAndHide.paymanage ? 'minus' : 'plus'}` }
                        onClick={()=>{props.partToggle('paymanage')}}>
                    </span>
                </p>
            </section>
            
            {
                props.PartShowAndHide.paymanage
                &&
                    <section className="part-content">
                        {/* standard-payment-info-table */}
                        <table className="table standard-payment-info-table mb-20">
                            <tr>
                                <th></th>
                                <td colSpan="4" className='ta-l'>{contractDate}</td>
                                <th></th>
                                <td colSpan="3" className='ta-l'>{contractChangeDate}</td>
                            </tr>
                            <tr>
                                <th rowSpan="3"></th>
                                <th></th>
                                <td colSpan="3">
                                    <select className="common-input w-100p" name="consultManager" value={consultManager} onChange={payDefaultInputHandler}>
                                        <option value=""></option>
                                        {employee.map((el,idx)=>(<option key={idx} value={el}>{el}</option>))}
                                    </select>
                                </td>
                                <th></th>
                                <td>
                                    <select className="common-input w-100p" name="consultLawyer" value={consultLawyer} onChange={payDefaultInputHandler}>
                                        <option value=""></option>
                                        {lawyer.map((el,idx)=>(<option key={idx} value={el}>{el}</option>))}
                                    </select>
                                </td>
                                <th></th>
                                <td>
                                    <select className="common-input w-100p" name="isNew" value={isNew} onChange={payDefaultInputHandler}>
                                        <option value=""></option>
                                        <option value=""></option>
                                    </select>
                                </td>
                            </tr>
                            <tr>
                                <th></th>
                                <td>{userName}</td>
                                <th><i className="required"></i></th>
                                <td>
                                    <input type="text" 
                                    name="allPay"
                                    value={allPay}
                                    onChange={payDefaultInputHandler}
                                    className="common-input w-100p mb-5 pay_required_01" 
                                    data-required=""/>
                                    <p style={greyFont}>* ,     .</p>
                                </td>
                                <th></th>
                                <td>
                                    <input type="text" 
                                    name="changePay"
                                    value={changePay}
                                    onChange={payDefaultInputHandler}
                                    className="common-input w-100p mb-5"/>
                                </td>
                                <th rowSpan='2'><i className="required"></i> </th>
                                <td rowSpan='2'>
                                    <select 
                                    data-required=" "
                                    className="common-input w-100p pay_required_01" 
                                    id="pay-method-selection" 
                                    onChange={payMethodFn}>
                                        <option value=""></option>
                                        <option value="" selected></option>
                                        <option value=""></option>
                                    </select>
                                </td>
                            </tr>
                            <tr>
                                <th></th>
                                <td colSpan='5'>
                                    <input 
                                        name="etcText"
                                        value={etcText}
                                        onChange={payDefaultInputHandler}
                                        type="text" 
                                        className="common-input" 
                                        style={widthCalc}/>
                                    <button 
                                    onClick={()=>{submitPayDefaultInfo('pay_required_01')}}
                                    className="default-btn-blue ml-5"></button>
                                </td>
                            </tr>
                        </table>
                        {/* // standard-payment-info-table */}
                        
                        {
                            Paymethod === ''
                            &&  
                                /* ''   table show */
                                <table className="table pay-at-once-table">
                                    <tr><th colSpan='6'> </th></tr>
                                    <tr>
                                        <th><i className="required"></i></th>
                                        <td>
                                            <input type="text" 
                                            data-required=""
                                            name="depositMoney"
                                            value={depositMoney}
                                            onChange={depositHandler}
                                            className="common-input w-100p allPay_required"/>
                                        </td>
                                        <th><i className="required"></i></th>
                                        <td><DatepickerCal dateRefresh={payDateChange}/></td>
                                        <th><i className="required"></i></th>
                                        <td>
                                            <select 
                                            data-required=""
                                            name="howPay"
                                            value={howPay}
                                            onChange={depositHandler}
                                            className="common-input mr-5 allPay_required" 
                                            style={widthCalc2}>
                                                <option value=""></option>
                                                <option value=""></option>
                                                <option value=""></option>
                                                <option value=""></option>
                                                <option value=""></option>
                                            </select>
                                            <select 
                                            data-required=""
                                            name="cards"
                                            value={cards}
                                            onChange={depositHandler}
                                            disabled={howPay === "" ? false : true}
                                            className="common-input ml-5 allPay_required" 
                                            style={widthCalc2}>
                                                <option value=""></option>
                                                {
                                                    creditCards.map((el, idx) => (
                                                        <option value={el.cardCode} key={idx}>{el.name}</option>
                                                    ))
                                                }
                                            </select>
                                        </td>
                                    </tr>
                                    <tr>
                                        <th><i className="required"></i> </th>
                                        <td>
                                            <select 
                                            data-required=""
                                            name="depositPaysort"
                                            value={depositPaysort}
                                            onChange={depositHandler}
                                            className="common-input w-100p allPay_required">
                                                <option value=""></option>
                                                <option value=""></option>
                                                <option value=""></option>
                                                <option value=""></option>
                                                <option value=""></option>
                                            </select>
                                        </td>
                                        <th><i className="required"></i></th>
                                        <td>
                                            <select 
                                            data-required=""
                                            name="paper"
                                            value={paper}
                                            onChange={depositHandler}
                                            disabled={howPay === ""}
                                            className="common-input w-100p allPay_required">
                                                <option value=""></option>
                                                <option value=""></option>
                                                <option value=""></option>
                                                <option value=""></option>
                                            </select>
                                        </td>
                                        <th></th>
                                        <td>
                                            <input type="text" 
                                            disabled={howPay === ""}
                                            name="issueNum"
                                            value={issueNum}
                                            onChange={depositHandler}
                                            className="common-input w-100p"/>
                                        </td>
                                    </tr>
                                    <tr>
                                        <th></th>
                                        <td colSpan='5'>
                                            <input type="text" 
                                            name="depositEtcCon"
                                            value={depositEtcCon}
                                            onChange={depositHandler}
                                            className='common-input w-100p'/>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td colSpan='6'>
                                            <button 
                                            onClick={()=>{submitPayDefaultInfo('allPay_required')}}
                                            className="default-btn-blue big-btn mt-5 mb-5"> </button>
                                        </td>
                                    </tr>
                                </table>
                                /* // ''  table show */
                        }
                        {
                            Paymethod === ''
                            &&
                                /* ''   table show */
                                <table className="table pay-divide-table ">
                                    <tr>
                                        <th colSpan='6' className='p-r'>
                                             
                                            <span className="default-btn-green" 
                                            style={btnStyles}
                                            onClick={()=>{dividePayInfoHandler('plus')}}
                                            ></span>
                                        </th>
                                    </tr>
                                    {
                                        DividePayInfo.map((el, idx) => (
                                            <tr key={idx}>  
                                                <th>
                                                    {idx === 0 && ' '}
                                                </th>
                                                <td>
                                                    {
                                                        idx === 0 
                                                        && 
                                                        (  <select 
                                                            className="common-input w-100p" 
                                                            value={el.paySort} 
                                                            onChange={(e)=>{inputChangeHandler(e, idx, "paySort")}} >
                                                                <option value=""></option>
                                                                <option value=""></option>
                                                                <option value=""></option>
                                                                <option value=""></option>
                                                            </select>
                                                        )
                                                    }
                                                
                                                </td>
                                                <th><i className="required"></i></th>
                                                <td>
                                                    <input type="text" 
                                                    className="common-input w-100p divide_required" 
                                                    data-required=""
                                                    value={el.payAmount}
                                                    onChange={(e)=>{inputChangeHandler(e, idx, "payAmount")}}/>
                                                </td>
                                                <th><i className="required"></i></th>
                                                <td className="flex-con m-0">
                                                    <DatepickerCal dateRefresh={(date)=>{divideDateHandler(date, idx)}} /> 
                                                    <span 
                                                        className="default-btn-red ml-5" 
                                                        onClick={()=>{dividePayInfoHandler('minus', idx)}}
                                                    ></span>
                                                </td>
                                            </tr>
                                        ))
                                    }
                                    <tr>
                                        <td colSpan='6'>
                                            <button 
                                            onClick={()=>{submitPayDefaultInfo('divide_required')}}
                                            className="default-btn-blue big-btn mt-5 mb-5"> </button></td>
                                    </tr>
                                </table>
                                /* // ''  table show */
                        }

                        {/* pay-history-table */}
                        <table className="table pay-history-table mt-20">
                            <tr><th colSpan="14"></th></tr>
                            <tr>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th>-</th>
                            </tr>
                                <tr>
                                <td></td>
                                <td></td>
                                <td>2021-01-01</td>
                                <td>500,000</td>
                                <td>2021-01-01</td>
                                <td>0</td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td>X</td>
                                <td>
                                    <span onClick={()=>{setPayModal(true)}} className="default-btn-green mr-5"></span>
                                    <span className="default-btn-red" onClick={deletePayHistory}></span>
                                </td>
                                </tr>
                            <tr>
                                <th></th>
                                <th colSpan='2'></th>
                                <td colSpan='2'></td>
                                <th></th>
                                <td colSpan='2'></td>
                                <th></th>
                                <td colSpan='2'></td>
                                <th></th>
                                <td colSpan='2' style={colorRed}></td>
                            </tr>
                        </table>
                        { payModal && <PayUpdateModal setPayModal={setPayModal}/> }
                        
                        {/* // pay-history-table */}

                        <table className="table mt-20">
                            <th style={widthDetail}>  </th>
                            <td className="ta-l" style={borderTopGrey} colSpan="5">
                                <select className="common-input" style={mw_200}>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                </select>
                            </td>
                        </table>
                    </section>
            } 
            {modal.status && <Modal refreshFunction={closeModal} modalText={modal.text}/>}

        </div>
    )
}

export default PaymanagePart
