import React, { useState } from 'react'
import { parseOnlyNumber } from '../../utils/unitParse';
import CaseCodeBox from './Sections/CaseCodeBox';

function CaseCodeSettingPage() {
    
    const [caseCodeLists, setCaseCodeLists] = useState([
        {caseCodeKeyV: 1234, caseCodeBig: '', caseCodeSort: '', caseCodeV: 'Aa', expectedMonth: 4, sortNumber: 1010},
        {caseCodeKeyV: 5678, caseCodeBig: '', caseCodeSort: '', caseCodeV: 'Ac', expectedMonth: 1, sortNumber: 1030},
    ]);

    const [caseCodeBig, setCaseCodeBig] = useState('');
    const [caseCodeSort, setCaseCodeSort] = useState('');
    const [caseCodeV, setCaseCodeV] = useState('');
    const [expectedMonth, setExpectedMonth] = useState();
    const [sortNumber, setSortNumber] = useState();

    const submitCaseCodeHandler = () => {
        if (!caseCodeBig || !caseCodeSort || !caseCodeV || !expectedMonth || !sortNumber) {
            return alert('  !');
        } 
        let dataToSubmit = {
            caseCodeBig,
            caseCodeSort,
            caseCodeV,
            expectedMonth,
            sortNumber
        };
        console.log('   ', dataToSubmit);
    };

    return (
        <div className='page-container'>
            {/*   start */}
             <div className="common-part mb-30">
                <section className="part-title clearfix">
                    <p className="title-txt"> </p>
                </section>
                <section className="part-content">
                    <table className="table card-plus-table">
                        <tr>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th>()</th>
                            <th>(1~99999)</th>
                        </tr>
                        <tr>
                            <td>
                                <input type="text" 
                                value={caseCodeBig}
                                onChange={(e)=>{setCaseCodeBig(e.target.value)}}
                                className="common-input w-170"/>
                            </td>
                            <td>
                                <input type="text" 
                                value={caseCodeSort}
                                onChange={(e)=>{setCaseCodeSort(e.target.value)}}
                                className="common-input w-170"/>
                            </td>
                            <td>
                                <input type="text" 
                                value={caseCodeV}
                                onChange={(e)=>{setCaseCodeV(e.target.value)}}
                                className="common-input w-170"/>
                            </td>
                            <td>
                                <input type="text" 
                                value={expectedMonth}
                                maxLength="4"
                                onChange={(e)=>{setExpectedMonth(parseOnlyNumber(e.target.value))}}
                                className="common-input w-170"/>
                            </td>
                            <td>
                                <input type="text" 
                                value={sortNumber}
                                onChange={(e)=>{setSortNumber(parseOnlyNumber(e.target.value))}}
                                maxLength="5"
                                className="common-input w-170"/>
                            </td>
                        </tr>
                        <tr>
                            <td colSpan="5">
                                <button
                                    onClick={submitCaseCodeHandler}
                                    className="default-btn-blue big-btn mt-5 mb-5">
                                </button>
                            </td>
                        </tr>
                    </table>
                </section>
            </div>
            {/*   end */}

            {/*   start*/}
            <div className="common-part">
                <section className="part-title clearfix">
                    <p className="title-txt"> </p>
                </section>
                <section className="part-content">
                    <table className="table card-manage-table">
                        <tr>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th>()</th>
                            <th></th>
                            <th></th>
                        </tr>

                        {caseCodeLists.length > 0 && caseCodeLists.map((el) => (
                            <CaseCodeBox key={el.caseCodeKeyV} caseCodeInfo={el}/>
                        ))}
                    </table>
                </section>
            </div>
            {/*   end*/}
        </div>
    )
}

export default CaseCodeSettingPage
