let koreaAreaData1 = ["", "", "", ""];
let koreaAreaData2 = {
  seoul: [ "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""],
  busan: [ "","","","","","","","","","","","","","","",""],
  daegu: [ "", "", "", "", "", "", "", ""],

};

export const getDong1 = () => {
  let results = [];
  for (let i = 0; i < koreaAreaData1.length; i++) {
    results.push(
      <option value={koreaAreaData1[i]}>{koreaAreaData1[i]}</option>
    );
  }
  return results;
};

export const getDong2 = (selectedValue, resultHtml, abroadInputs) => {
    let returnTarget = document.querySelector(`.${resultHtml}`);
    returnTarget.options.length = 0;
    if (selectedValue === '') {
        returnTarget.style.display = 'none';
        document.querySelector(`.${abroadInputs}`).style.display = 'inline-block';
    } else {
        returnTarget.style.display = 'inline-block';
        document.querySelector(`.${abroadInputs}`).style.display = 'none';
        if (selectedValue !== '') {
            switch (selectedValue) {
                case '':
                    var subOption = koreaAreaData2.seoul;
                    break;
                case '':
                    var subOption = koreaAreaData2.busan;
                    break;
                case '':
                    var subOption = koreaAreaData2.daegu;
                    break;
            }

            for (let i = 0; i < subOption.length; i++) {
                var option = document.createElement('option');
                option.innerText = subOption[i];
                option.value = subOption[i];
                returnTarget.append(option);
            }    
        }
    }
};
