import React, { useState, useEffect, useMemo } from 'react'
import { caseName1, channel, consultResult } from '../../../../Datas/datas';
import { parseOnlyPhone } from '../../../utils/unitParse';
import { getDong1, getDong2 } from '../../../utils/getDong';
import ConsultHistory from '../Sections/ConsultHistory.js';
import Modal from '../../../utils/Modal';
import './JeopSuPart.css';

// fontawesome Icon
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes } from "@fortawesome/free-solid-svg-icons";

let showHideToggle = 0;

const dummyDatas1 = [
    {
      employeeName: "",
      employeeRole: 1,
      consultDate: "2021-01-01 12:00:00",
      consultCon: " 1",
    },
    {
      employeeName: "",
      employeeRole: 2,
      consultDate: "2021-01-01 12:00:00",
      consultCon: " 1",
    },
    {
      employeeName: "",
      employeeRole: 3,
      consultDate: "2021-01-01 12:00:00",
      consultCon: " 1",
    },
    {
      employeeName: "",
      employeeRole: 4,
      consultDate: "2021-01-01 12:00:00",
      consultCon: " 1",
    },
  ];
function JeopSuPart(props) {
    const [modal, setModal] = useState({ status: false, text: '' }); //  state
    const [pastConsultModal, setPastConsultModal] = useState(false); //    state
    const [uniquenessDirect, setUniquenessDirect] = useState(''); //  
    const [jeopsuContents, setJeopsuContents] = useState(''); //   state
    
    const [standardsInput, setStandardsInput] = useState({  //    state
        jeopsuEmployee: '',
        userRelation: '',
        userArea1: '',
        userArea2: '',
        userName2: '',
        userPhone2: '',
        userGender: '',
        isNewUser: '',
        isCp: false,
    });
    
    const { 
        jeopsuEmployee, 
        userRelation, 
        userArea1,
        userArea2,
        userName2,
        userPhone2,
        userGender,
        isNewUser,
        isCp
    } = standardsInput;
    
    const [detailsInput, setDetailsInput] = useState({  //    state
        uniqueness: '',
        question: '',
        cp1: '',
        cp2: '',
        userJob: '',
        caseBig: '',
        caseAgoDB: '',
        userType: '',
        routes: '',
        recommendEmployee: '',
        routesDetail: '',
        keyWords: '',
        sources: '',
        medias: '',
        oneStepResults: ''
    });

    const { 
        uniqueness,
        question,
        cp1,
        cp2,
        userJob,
        caseBig,
        caseAgoDB,
        userType,
        routes,
        recommendEmployee,
        routesDetail,
        keyWords,
        sources,
        medias,
        oneStepResults
    } = detailsInput;

    const closeModal = () => { setModal(false); }; //   

    const inputHandler1 = (e) => { //    
        let {name, value} = e.target;
        if (name == 'userPhone2') value = parseOnlyPhone(value);
        else if (name == 'isCp')  value = !isCp;

        setStandardsInput({
            ...standardsInput,
            [name]: value
        });
    };

    const inputHandler2 = (e) => { //    
        let {name, value} = e.target;
        setDetailsInput({
            ...detailsInput,
            [name]: value
        });
    };

    const checkRequired = (classNames) => { //   
        let checkEl = document.querySelectorAll(`.${classNames}`);
        for (let i = 0; i < checkEl.length; i++) {
            if (checkEl[i].value === "" && !checkEl[i].disabled) {
                let copy = {...modal};
                copy.status = true;
                copy.text = `${checkEl[i].getAttribute('data-required')} .`
                setModal(copy);
                return false;
            }
        }
        return true;
    }
    
    const showHideAllSection = () => { //    /   
        let copy = {...props.PartShowAndHide};
        if (showHideToggle === 0) {
            copy = { jeopsu: false, sangdam: false, visitSchedule: false, paymanage: false, contractManage: false, caseManage: false };
            showHideToggle = 1;
        } else {
            copy ={ jeopsu: true, sangdam: true, visitSchedule: true, paymanage: true, contractManage: true, caseManage: true };
            showHideToggle = 0;
        }
        props.setPartShowAndHide(copy);
    };

    const submitDefaultInfo = () => { //   submit 
        if ( checkRequired('required_1') ) {
            let dataToSumbit = Object.assign(standardsInput, { userName: props.userName, userPhone: props.userPhone });
            console.log('  ', dataToSumbit);
        }
    };

    const jeopsuConSubmitHandler = () => { //  submit 
        if (jeopsuContents.trim() === "" ) {
            let copy = {...modal};
            copy.status = true;
            copy.text = ' .';
            setModal(copy);
            return false;
        }
        if ( checkRequired('required_1') ) {
            console.log(' DB', jeopsuContents);
        }
    };

    const submitJeopsuAll = () => { //    submit 
        if ( checkRequired('required_2') ) {

            if (detailsInput.routes !== "") detailsInput.recommendEmployee = "";
            
            if (detailsInput.uniqueness === "") detailsInput.uniqueness = uniquenessDirect;
            else detailsInput.uniqueness = uniqueness;

            let dataToSumbit = Object.assign(standardsInput, detailsInput, { userName: props.userName, userPhone: props.userPhone });
            console.log('    ', dataToSumbit);
        }
    }

    useEffect(() => {
        getDong2(document.querySelector('.get-dong-1').value, 'getDongResults1', 'inputNational1');
    }, []);

    const style1 = useMemo(() => ({ padding: '0 20px', fontSize: '15px'}), []);
    const style2 = useMemo(() => ({width: 'calc(50% - 5px)'}), []);
    const style3 = useMemo(() => ({ width: '90%', verticalAlign: '-12px', height: '120px' }), []);
    const style4 = useMemo(() => ({width: 'calc(50% - 5px)', display: 'none'}), []);
    const style5 = useMemo(() => ({ marginTop: '-20px'}), []);

    return (
        <div className="common-part mb-20" style={style5}>
            <section className="part-title clearfix">
                <p className="title-txt"></p>
                <p className="btn-wrapper">
                    <span className="default-btn-theme"></span>
                    <span className="default-btn-theme ml-5" onClick={showHideAllSection}>
                        {showHideToggle === 0 ? '' : ''}
                    </span>
                    <span 
                        className={ `default-btn-theme ml-5 ${props.PartShowAndHide.jeopsu ? 'minus' : 'plus'}` }
                        onClick={()=>{props.partToggle('jeopsu')}}>
                    </span>
                </p>
            </section>

            {
                props.PartShowAndHide.jeopsu
                &&
                    <section className="part-content">
                    {/* standard-jeopsu-info-table */}
                    <table className="table standard-jeopsu-info-table">
                        <colgroup>
                            <col className="w-10p"/>
                            <col className="w-7p"/>
                            <col className="w-13p"/>
                            <col className="w-7p"/>
                            <col className="w-15p"/>
                            <col className="w-10p"/>
                            <col className="w-14p"/>
                            <col className="w-7p"/>
                            <col className="w-6p"/>
                            <col className="w-6p"/>
                            <col className="w-5p"/>
                        </colgroup>
                        <tr>
                            <th colSpan="4"></th>
                            <td colSpan="7" className='ta-l'>
                                <select className="common-input mr-10" name="jeopsuEmployee" value={jeopsuEmployee} onChange={inputHandler1}>
                                    <option value=""></option>
                                    <option value="1">1</option>
                                    <option value="2">2</option>
                                    <option value="3">3</option>
                                </select>
                                <button className="default-btn-blue" onClick={submitDefaultInfo}>  </button>
                            </td>
                        </tr>
                        <tr>
                            <th rowSpan="2"></th>
                            <th><i className="required"></i></th>
                            <td><input type="text" className="common-input w-100p required_1 required_2" data-required="" value={props.userName} onChange={(e)=>{props.setUserName(e.target.value)}}/></td>
                            <th><i className="required"></i></th>
                            <td>
                                <input type="text" className="common-input w-100p required_1 required_2" maxlength="15" data-required=""
                                value={props.userPhone} 
                                onChange={(e)=>{props.setUserPhone(parseOnlyPhone(e.target.value))}}    
                                /></td>
                            <th></th>
                            <td>
                                <select className="common-input w-100p" name="userRelation" value={userRelation} onChange={inputHandler1}>
                                    <option value=""></option>
                                    <option value=""></option>
                                </select>
                            </td>
                            <th></th>
                            <td colSpan="3">
                                <select 
                                    className="common-input mr-5 get-dong-1" 
                                    style={style2} 
                                    name="userArea1" 
                                    value={userArea1}
                                    onChange={
                                        (e)=>{
                                            getDong2(e.target.value, 'getDongResults1', 'inputNational1');
                                            inputHandler1(e);
                                        }
                                    }
                                    >
                                    <option value=""></option>
                                    {getDong1()}
                                </select>
                                <select className="common-input ml-5 getDongResults1" style={style2} name="userArea2" value={userArea2} onChange={inputHandler1}>
                                </select>
                                <input type="text" placeholder="" name="userArea2" value={userArea2} onChange={inputHandler1} className="common-input ml-5 inputNational1" style={style4} />
                            </td>
                        </tr>
                        <tr>
                            <th>()</th>
                            <td><input type="text" className="common-input w-100p" name="userName2" value={userName2} onChange={inputHandler1} /></td>
                            <th>()</th>
                            <td><input type="text" className="common-input w-100p" name="userPhone2" value={userPhone2} onChange={inputHandler1} maxlength="15"/></td>
                            <th></th>
                            <td>
                                <select className="common-input w-100p" name="userGender" value={userGender} onChange={inputHandler1}>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                </select>
                            </td>
                            <th></th>
                            <td>
                                <select className="common-input w-100p" name="isNewUser" value={isNewUser} onChange={inputHandler1}>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                </select>
                            </td>
                            <th>CP</th>
                            <td><input type="checkbox" name="isCp" onChange={inputHandler1} checked={isCp}/></td>
                        </tr>

                    </table>
                    {/* //standard-jeopsu-info-table */}

                    {/* past-consult-table */}
                    <table className="table past-consult-table mt-20">
                        <tr><th colSpan="5"></th></tr>
                        <tr>
                            <td>2021-09-01</td>
                            <td><span className="default-btn-green" onClick={()=>{setPastConsultModal(true)}}></span></td>
                            <td></td>
                            <td></td>
                            <td><span className="default-btn-green"></span></td>
                        </tr>
                        <tr>
                            <td>2021-09-01</td>
                            <td><span className="default-btn-green" onClick={()=>{setPastConsultModal(true)}}></span></td>
                            <td></td>
                            <td></td>
                            <td><span className="default-btn-green"></span></td>
                        </tr>
                    </table>
                    { pastConsultModal && <PastConsult setPastConsultModal={setPastConsultModal}/> }
                    {/* // past-consult-table */}

                    {/* //detail-jeopsu-info-table */}
                    <table className='table detail-jeopsu-info-table mt-10'>
                        <tr>
                            <th rowSpan="2"></th>
                            <td colSpan="8" className='p-10'>
                                <ConsultHistory dummyDatas={dummyDatas1}/>
                            </td>
                        </tr>        
                        <tr>
                            <th></th>
                            <td colSpan="7" className="p-10">
                                <textarea 
                                className="common-input" 
                                style={style3} 
                                value={jeopsuContents}
                                onChange={(e)=>{setJeopsuContents(e.target.value)}}
                                ></textarea>
                                <button 
                                    className="default-btn-green big-btn ml-5" 
                                    onClick={jeopsuConSubmitHandler}>
                                    
                                </button>
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td colSpan="8" className="ta-l">
                                <select className="common-input noted-selection" name="uniqueness" value={uniqueness} onChange={inputHandler2}>
                                    <option value=""></option>
                                    <option value=" "> </option>
                                    <option value="  ">  </option>
                                    <option value=" "> </option>
                                    <option value="02  ">02  </option>
                                    <option value="010  ">010  </option>
                                    <option value=" "> </option>
                                    <option value="30   ">30   </option>
                                    <option value=" "> </option>
                                    <option value=""></option>
                                </select>
                                <br />
                                <input type="text" 
                                value={uniqueness === "" ? uniquenessDirect : uniqueness}
                                readOnly={uniqueness !== "" && 'true'}
                                onChange={(e)=>{setUniquenessDirect(e.target.value)}}
                                className="common-input mt-5 w-100p" />
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td colSpan="8">
                                <textarea className="common-input w-100p" name="question" value={question} onChange={inputHandler2}></textarea>
                            </td>
                        </tr>
                        <tr>
                            <th>CP</th>
                            <td colSpan="8" className="ta-l">
                                <select className="common-input" name="cp1" value={cp1} onChange={inputHandler2}>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value="  ">  </option>
                                    <option value="  ">  </option>
                                    <option value="  ">  </option>
                                    <option value="    ">    </option>
                                    <option value="   ">   </option>
                                    <option value="  ">  </option>
                                    <option value=" "> </option>
                                </select>     
                                <br />
                                <textarea className="common-input w-100p mt-5" name="cp2" value={cp2} onChange={inputHandler2}></textarea>
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <th></th>
                            <td>
                                <select className="common-input w-100p" name="userJob" value={userJob} onChange={inputHandler2}>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                </select>
                            </td>
                            <th>()</th>
                            <td>
                                <select className="common-input w-100p" name="caseBig" value={caseBig} onChange={inputHandler2}>
                                    {caseName1.map((el,idx)=> (<option value={el}>{el}</option>))}
                                </select>
                            </td>
                            <th>()</th>
                            <td><input type="text" className="common-input w-100p" name="caseAgoDB" value={caseAgoDB} onChange={inputHandler2}/></td>
                            <th></th>
                            <td>
                                <select className="common-input w-100p" name="userType" value={userType} onChange={inputHandler2}>
                                    <option value=""></option>
                                    <option value=""></option>
                                </select>
                            </td>
                        </tr>
                        <tr>
                            <th rowSpan="2"></th>
                            <th><i className="required"></i></th>
                            <td>
                                <select className="common-input w-100p required_2" data-required="" name="routes" value={routes} onChange={inputHandler2}>
                                    {channel.map((el, idx)=> (
                                        <option key={idx} value={el}>{el}</option>
                                    ))}
                                </select>
                            </td>
                            <th></th>
                            <td>
                                <input type="text" 
                                className="common-input w-100p"
                                readOnly={routes !== "" && 'true'}
                                name="recommendEmployee" 
                                value={routes === "" ? recommendEmployee : ''} 
                                onChange={inputHandler2}
                                />
                            </td>
                            <th>()</th>
                            <td colSpan="3"><input type="text" className="common-input w-100p" name="routesDetail" value={routesDetail} onChange={inputHandler2} /></td>
                        </tr>
                        <tr>
                            <th>()</th>
                            <td><input type="text" className="common-input w-100p" name="keyWords" value={keyWords} onChange={inputHandler2} /></td>
                            <th></th>
                            <td colSpan="2"><input type="text" className="common-input w-100p" name="sources" value={sources} onChange={inputHandler2} /></td>
                            <th></th>
                            <td colSpan="2"><input type="text" className="common-input w-100p" name="medias" value={medias} onChange={inputHandler2}/></td>
                        </tr>
                        <tr>
                            <th>1  </th>
                            <td colSpan="8" className="ta-l">
                                <select className="common-input" name="oneStepResults" value={oneStepResults} onChange={inputHandler2}>
                                    {
                                        consultResult.map((el, idx) => (
                                            <option value={el}>{el}</option>
                                        ))
                                    }
                                </select>
                                <span className="default-btn-blue ml-5" style={style1} onClick={submitJeopsuAll}></span>
                            </td>
                        </tr>
                    </table>
                    {/* //detail-jeopsu-info-table */}

                </section>
            }
            
        {modal.status && <Modal refreshFunction={closeModal} modalText={modal.text}/>}
        </div>
    )
}

function PastConsult(props) {
    return (
        <div className="past-consult-modal">
            <p className='btn-wrap'>
                <span className='close-modal' onClick={()=>{props.setPastConsultModal(false)}}><FontAwesomeIcon icon={faTimes}/></span>
            </p>
            <p className="consult-date"> : 2021-08-11</p>
            <p className="consult-info mb-30">
                <span> : </span> /
                <span> : pass 1</span> /
                <span> : </span>
            </p>

            <table className='table past-jeopsu-consult mb-10'>
                <tr>
                    <th></th>
                    <th></th>
                    <th> </th>
                    <th></th>
                </tr>
                <tr>
                    <td>2021-01-01 00:00:00</td>
                    <td></td>
                    <td></td>
                    <td></td>
                </tr>
            </table>

            <table className='table past-call-consult mb-10'>
                <tr>
                    <th></th>
                    <th></th>
                    <th> </th>
                    <th></th>
                </tr>
                <tr>
                    <td colSpan="4">  .</td>
                </tr>
            </table>

            <table className='table past-visit-consult mb-10'>
                <tr>
                    <th></th>
                    <th></th>
                    <th> </th>
                    <th></th>
                </tr>
                <tr>
                    <td colSpan="4">  .</td>
                </tr>
            </table>

        </div>
    )
}

export default JeopSuPart