import React, { useState, useEffect } from 'react'
import Modal from '../../../utils/Modal';
import DatepickerCal from '../../../utils/DatepickerCal';
import { creditCards } from '../../../../Datas/datas';
import { parseCurrency } from '../../../utils/unitParse';
import { currentDate } from '../../../utils/time';
import styled from 'styled-components';

// fontawesome Icon
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes } from "@fortawesome/free-solid-svg-icons";

const ModalWrapper = styled.div`
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 500;
    width: 815px;
    background: #fff;
    padding: 30px;
    border: 1px solid #eee;
`;
const CloseButton = styled.span`
    cursor: pointer; 
    float: right;
    font-size: 22px;
    margin-top:1px;
`;

function PayUpdateModal(props) {

    const [modal, setModal] = useState({ status: false, text: '' }); //  state

    const [payUpdateInfo, setPayUpdateInfo] = useState({ //    state
        payStatus: '',
        payMethod: '',
        paySort: '',
        depositDate: currentDate(),
        priceAmount: '',
        depositor: '',
        howPay: '',
        cards: '',
        paper: '',
        issueNum: '',
        issueDate: currentDate(),
        isConfirm: false,
        calculateDate: currentDate(),
        calculatePrice: '',
        etcCon: ''
    }); 

    const {
        payStatus,
        payMethod,
        paySort,
        priceAmount,
        depositor,
        howPay,
        cards,
        paper,
        issueNum,
        isConfirm,
        calculatePrice,
        etcCon
    } = payUpdateInfo;

    const inputHandler = (e) => { //   
        let {name, value} = e.target;
        if ( ["priceAmount", "calculatePrice"].indexOf(name) > -1 ) value = parseCurrency(value);
        else if ( name == 'isConfirm' ) value = !isConfirm;
        else if (name == 'howPay') {
            if (value == '' ) {
                payUpdateInfo.paper = "";
                payUpdateInfo.issueNum = "";
                payUpdateInfo.issueDate = "";
            } else {
                payUpdateInfo.cards = "";
            }
        }
        setPayUpdateInfo({
            ...payUpdateInfo,
            [name]: value
        })
    };

    const checkRequired = (classNames) => { //   
        let checkEl = document.querySelectorAll(`.${classNames}`);
        for (let i = 0; i < checkEl.length; i++) {
            if (checkEl[i].value === "" && !checkEl[i].disabled) {
                let copy = {...modal};
                copy.status = true;
                copy.text = `${checkEl[i].getAttribute('data-required')} .`
                setModal(copy);
                return false;
            }
        }
        return true;
    };

    const payDateChange = (date, types) => { 
        let copy = {...payUpdateInfo};
        copy[types] = date;
        setPayUpdateInfo(copy);
    };
    
    const submitHandler = () => {
        if ( checkRequired('required_pay_update') ) {
            console.log('   ', payUpdateInfo);
        }
    }

    const closeModal = () => { setModal(false); }; //   

    return (
        <ModalWrapper className="common-part pay-update-modal">
             <section className="part-title clearfix">
                <p className="title-txt"> </p>
                <CloseButton onClick={()=>{props.setPayModal(false)}}><FontAwesomeIcon icon={faTimes} className="close-update-modal"/></CloseButton>
            </section>
            <section className="part-content">
                <table className="table pay-update-table">
                    <tr>
                        <th></th>
                        <td colSpan="3" className="ta-l">
                            <select 
                            name="payStatus"
                            value={payStatus}
                            onChange={inputHandler}
                            className="common-input">
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                            </select>
                        </td>
                    </tr>
                    <tr>
                        <th><i className="required"></i></th>
                        <td>
                            <select 
                            name="payMethod"
                            value={payMethod}
                            onChange={inputHandler}
                            data-required=""
                            className="common-input w-100p required_pay_update">
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                            </select>
                        </td>
                        <th><i className="required"></i></th>
                        <td>
                            <select 
                            name="paySort"
                            value={paySort}
                            data-required=""
                            onChange={inputHandler}
                            className="common-input w-100p required_pay_update">
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                            </select>
                        </td>
                    </tr>
                    <tr>
                        <th><i className="required"></i></th>
                        <td><DatepickerCal dateRefresh={(date)=>{payDateChange(date, "depositDate")}}/></td>
                        <th><i className="required"></i></th>
                        <td>
                            <input type="text" 
                            name="priceAmount"
                            value={priceAmount}
                            onChange={inputHandler}
                            data-required=""
                            className="common-input w-100p required_pay_update"/>
                        </td>
                    </tr>
                    <tr>
                        <th></th>
                        <td>
                            <input type="text" 
                            name="depositor"
                            value={depositor}
                            onChange={inputHandler}
                            className="common-input w-100p" />
                        </td>
                        <th></th>
                        <td></td>
                    </tr>
                    <tr>
                        <th><i className="required"></i></th>
                        <td>
                            <select 
                            name="howPay"
                            value={howPay}
                            onChange={inputHandler}
                            data-required=""
                            className="common-input w-100p required_pay_update">
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                            </select>
                        </td>
                        <th><i className="required"></i></th>
                        <td>
                            <select 
                            name="cards"
                            value={cards}
                            onChange={inputHandler}
                            data-required=""
                            disabled={howPay === "" ? false : true}
                            className="common-input w-100p required_pay_update">
                                <option value=""></option>
                                {
                                    creditCards.map((el, idx) => (
                                        <option value={el.cardCode} key={idx}>{el.name}</option>
                                    ))
                                }
                            </select>
                        </td>   
                    </tr>
                    <tr>
                        <th><i className="required"></i></th>
                        <td>
                            <select 
                            name="paper"
                            value={paper}
                            onChange={inputHandler}
                            data-required=""
                            disabled={howPay === ""}
                            className="common-input w-100p required_pay_update">
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                            </select>
                        </td>   
                        <th></th>
                        <td>
                            <input type="text" 
                            name="issueNum"
                            disabled={howPay === ""}
                            value={issueNum}
                            onChange={inputHandler}
                            className="common-input w-100p"/>
                        </td>
                    </tr>
                    <tr>
                        <th></th>
                        <td><DatepickerCal disabled={howPay === ""} dateRefresh={(date)=>{payDateChange(date, "issueDate")}}/></td>
                        <th></th>
                        <td className='ta-l'>
                            <input type="checkbox" name="isConfirm" checked={isConfirm} onChange={inputHandler} />
                        </td>
                    </tr>
                    <tr>
                        <th></th>
                        <td><DatepickerCal dateRefresh={(date)=>{payDateChange(date, "calculateDate")}}/></td>
                        <th></th>
                        <td>
                            <input type="text" 
                            name="calculatePrice"
                            value={calculatePrice}
                            onChange={inputHandler}
                            className="common-input w-100p"/>
                        </td>
                    </tr>
                    <tr>
                        <th></th>
                        <td colSpan="3">
                            <input type="text" 
                            name="etcCon"
                            value={etcCon}
                            onChange={inputHandler}
                            className="common-input w-100p"/>
                        </td>
                    </tr>
                    <tr>
                        <td colSpan='4'>
                            <button 
                            onClick={submitHandler}
                            className="default-btn-blue big-btn mt-5 mb-5"> </button>
                        </td>
                    </tr>
                </table>
            </section>
            {modal.status && <Modal refreshFunction={closeModal} modalText={modal.text}/>}
        </ModalWrapper>
    )
}

export default PayUpdateModal
