import React, { useMemo } from "react";
import SearchBox from "../../utils/SearchBox";

function NotPayListPage() {
    const renderTable = () => {
        const results = [];
        for (let i = 0; i < 20; i++) {
            results.push(
                <tr key={i}>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                </tr>
            );
        }
        return results;
    }
    const heightStyle = useMemo(() => ({height: 'calc(100vh - 577px)'}), []);

    return (
        <div className="page-container">
            <SearchBox option={{
                dateStart: true,
                dateEnd: true,
                searchText: true
            }}/>

            <div className="table-title clearfix">
                <p className="title-txt"></p>
            </div>  
            
            <table className="table sales_summary mb-30">
                <tr className="first-row">
                    <th colSpan='8'></th>
                </tr>
                <tr>
                    <th className='border-top-none'></th>
                    <td></td>
                    <th className='border-top-none'></th>
                    <td></td>
                    <th className='border-top-none'></th>
                    <td></td>
                    <th className='border-top-none'></th>
                    <td></td>
                </tr>
            </table>

            <div className="table_wrapper" style={heightStyle}>
                <table className="table">
                    <thead>
                        <tr>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        {renderTable()}
                    </tbody>
                </table>
            </div>
        </div>
        
    )
}

export default NotPayListPage
