import React from "react";
import { Link } from 'react-router-dom';

function MemberListPage() {

    const renderMemberLists = () => {
        const results = [];
        for(let i = 0; i < 50; i++) {
            results.push(
                <tr key={i}>
                    <td>{50-i}</td>
                    <td><Link to={`/memberList/${i}`} className='bolder'></Link></td>
                    <td></td>
                    <td></td>
                    <td>1</td>
                    <td></td>
                    <td>abcdefg</td>
                    <td></td>
                    <td></td>
                    <td><span className="default-btn-theme"></span></td>
                </tr>
            )
        }
        return results
    }
    return (
        <div className="page-container memberlist-page">

            <div className="table-title clearfix mt-0">
                <p className="title-txt"> </p>

                <Link to="/memberList/addMember" className="default-btn-theme"></Link>
            </div>  
        
            <div className="table_wrapper one_table mt-30">
                <table className="table">
                    <thead>
                        <tr>
                            <th>#</th>
                            <th></th>
                            <th colSpan="3"></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        {renderMemberLists()}
                    </tbody>
                </table>
            </div>
        </div>
    )
}

export default MemberListPage
