//
export const comma = (str) => {
    str = String(str);
    return str.replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,');
}

//
export const uncomma = (str) => {
    str = String(str);
    return str.replace(/[^\d]+/g, '');
}

//  
export const parseCurrency = (value) => {  
    return comma(uncomma(value).replace(/[^0-9:\,]/gi, ""))
}

//   
export const parseOnlyNumber = (value) => {
    return value.replace(/[^0-9]/gi,"");
}

//    (, '-'  ) 
export const parseOnlyPhone = (value) => {
    return value.replace(/[^0-9:\-]/gi,"");
}

//   
export const parseOnlyKorean = (value) => {
    return value.replace(/[^-]/gi,"");
}

//  
export const parseNoSpaces = (value) => {
    return value.replace(/ /gi,"");
}
