import React from "react";
import SearchBox from "../../utils/SearchBox";
import { currentDate } from '../../utils/time';
import './Sections/DetailSalesListPage.css';

function DetailSalesListPage() {
    const renderTable = () => {
        const results = [];
        for (let i = 0; i < 20; i++) {
            results.push(
                <tr key={i}>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                </tr>
            );
        }
        return results;
    }

    return (
        <div className="page-container detail-sales-list">
            <SearchBox option={{
                dateStart: true,
                dateEnd: true,
                incomeKinds: true,
                jeopsuEmployee: true,
                searchText: true
            }}/>

            <div className="table-title clearfix">
                <p className="title-txt"></p>

                <a href="javascript:;" className="default-btn"></a>
            </div>  

            <div className="table_wrapper">
                <table className="table">
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th>()</th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th className="w-200"></th>
                    </tr>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th className="w-200"></th>
                    </tr>
                    <tr>
                        <th> ({currentDate().slice(-2)})</th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th className="w-200"></th>
                    </tr>
                </thead>
                <tbody>
                    {renderTable()}
                </tbody>
                </table>
            </div>
        </div>
        
    )
}

export default DetailSalesListPage
