import React, { useMemo } from "react";
import SearchBox from "../../utils/SearchBox";

function RefundListPage() {
    
    const redColor = useMemo(() => ({color: '#f30'}), []);

    const renderTable = () => {
        const results = [];
        for (let i = 0; i < 20; i++) {
            results.push(
                <tr key={i}>
                    <td>2021-01-01</td>
                    <td>2021-01-01</td>
                    <td></td>
                    <td></td>
                    <td>3,500,000</td>
                    <td>3,500,000</td>
                    <td style={redColor}>-1,750,000</td>
                    <td></td>
                </tr>
            );
        }
        return results;
    }

    return (
        <div className="page-container">
            <SearchBox option={{
                dateStart: true,
                dateEnd: true,
                searchText: true
            }}/>

            <div className="table-title clearfix">
                <p className="title-txt"></p>
            </div>  

            <div className="table_wrapper">
                <table className="table">
                    <thead>
                        <tr>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th>()</th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        {renderTable()}
                    </tbody>
                </table>
            </div>
        </div>
        
    )
}

export default RefundListPage
