import React, { useState } from "react";
import SearchBox from "../../utils/SearchBox";
import articleLists from '../../../Datas/articleLists5';
import { centerLists } from '../../../Datas/datas';
import { currentYear, currentMonth } from '../../utils/time';

function BaedangListPage() {
    const [Article, setArticle] = useState(articleLists);
    const [CheckBox, setCheckBox] = useState([]);

    const checkboxToggle = (value) => {
        const currentIdx = CheckBox.indexOf(value);
        const copy = [...CheckBox];
        if ( currentIdx === -1 ) {
          copy.push(value);
        } else {
          copy.splice(currentIdx, 1);
        }
        setCheckBox(copy);
      }

    const renderTable = () => {
        const results = [];
        for (let i = 0; i < 20; i++) {
            results.push(
                <tr key={i}>
                    <td>11111111</td>
                    <td></td>
                    <td>1111111111</td>
                </tr>
            );
        }
        return results;
    }

    const checkAll = (e) => {
        const isChecked = e.target.checked;
        if (isChecked) {
            const AllCheckLists = Array.from({length: articleLists.length}, (v, i) => i); 
            setCheckBox(AllCheckLists);
        } else {
            setCheckBox([]);
        }
    } 

    return (
        <div className="page-container baedang-list-page">
            {/* center-baedang */}
            <div className="center-baedang">
                <div className="table-title clearfix mt-0">
                    <p className="title-txt">{currentYear()} {currentMonth()}  </p>
                </div>  

                <table className="table">
                    <thead>
                        <tr>
                            <th colSpan="2" className="border-bottom-none"></th>
                            <th>/</th>
                            <th></th>
                            <th>1</th>
                            <th>2</th>
                            <th>3</th>
                            <th>4</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <th rowSpan="2" className="border-bottom-none"> </th>
                            <th className="border-bottom-none"></th>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr>
                            <th className="border-bottom-none"></th>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr>
                            <th rowSpan="2" className="border-bottom-none"></th>
                            <th className="border-bottom-none"></th>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr>
                            <th className="border-bottom-none"></th>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr>
                            <th rowSpan="2"></th>
                            <th className="border-bottom-none"></th>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr>
                            <th></th>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                    </tbody>

                </table>
            </div>
            {/* // center-baedang */}

            {/* day-baedang */}
            <div className="day-baedang">
                <div className="table-title clearfix">
                    <p className="title-txt"></p>

                    <a href="javascript:;" className="default-btn"></a>
                </div>  
            
                <SearchBox option={{
                    dateStart: true,
                    dateEnd: true,
                    searchText: true
                }}/>
                <div className="table_wrapper mt-30">
                    <table className="table">
                        <thead>
                            <tr>
                                <th><input type="checkbox" onChange={checkAll}/></th>
                                <th></th>
                                <th></th>
                                <th>()</th>
                                <th>()</th>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th className="w-75"></th>
                                <th className="w-85"></th>
                                <th className="w-75"></th>
                                <th className="w-75"></th>
                                <th></th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                Article.map((el, idx) => (
                                    <tr key={idx}>
                                        <td><input type="checkbox" onChange={()=>checkboxToggle(el._id)} checked={CheckBox.indexOf(el._id) === -1 ? '' : 'checked'}/></td>
                                        <td>{el.contractDate}</td>
                                        <td>{el.name}</td>
                                        <td>{el.caseName1}</td>
                                        <td>{el.caseName2}</td>
                                        <td>{el.caseRole}</td>
                                        <td>{el.contractMoney}</td>
                                        <td><a href="javascript:;" className="default-btn-theme"></a></td>
                                        <td>
                                            <a href="javascript:;" className="default-btn mr-5"></a>
                                            <a href="javascript:;" className="default-btn-theme"></a>
                                        </td>
                                        <td>{el.howUrgent}</td>
                                        <td>{el.howChangeable}</td>
                                        <td>{el.difficulty}</td>
                                        <td>{el.workload}</td>
                                        <td>
                                            <select className="common-input w-100p">
                                                <option value=""></option>
                                                {
                                                centerLists.map((option, optionIdx) => (
                                                    <option value={option} selected={el.baedangCenter == option && 'true'}>{option}</option>
                                                ))
                                                }
                                            </select>
                                        </td>
                                        <td>
                                            <select className="common-input w-100p">
                                                <option value=""></option>
                                                <option value=""></option>
                                                <option value=""></option>
                                            </select>
                                        </td>
                                    </tr>
                                ))
                            }
                        </tbody>
                    </table>
                </div>
            </div>
            {/* // day-baedang */}


        </div>
    )
}

export default BaedangListPage
