import React, { useState } from "react";
import SearchBox from "../../utils/SearchBox";
import styled from "styled-components";

const LongTh = styled.th`
  width: 117px;
`
function CaseListPage() {
  
  const renderTable = () => {
        const results = [];
        for (let i = 0; i < 20; i++) {
            results.push(
                <tr key={i}>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                </tr>
            );
        }
        return results;
  }

  return (
    <div className="page-container">
      <SearchBox option={{
        dateStart: true,
        dateEnd: true,
        isNew: true,
        jeopsuEmployee: true,
        searchText: true,
        caseName2: true
      }}/>

      <div className="table-title clearfix">
        <p className="title-txt"></p>

        <a href="javascript:;" className="default-btn-theme"></a>
      </div>

      <div className="table_wrapper">
        <table className="table">
          <thead>
            <tr>
                <th></th>
                <LongTh></LongTh>
                <th></th>
                <th>()</th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th>()</th>
                <th>()</th>
                <th></th>
            </tr>
          </thead>
          <tbody>
              {renderTable()}
          </tbody>
        </table>
      </div>

    </div>
  );
}

export default CaseListPage;
