import React, { useState, useMemo } from 'react';
import { caseName1, caseName2 } from '../../../../Datas/datas';
import Modal from '../../../utils/Modal';
import LegalReportTable from './LegalReportTable';
import { Link } from 'react-router-dom';
import {
    parseOnlyPhone,
    parseCurrency
} from '../../../utils/unitParse';
import styled from 'styled-components';

const CheckLists = styled.p`
    float: left;
    width: 25%;
    margin-top:5px;
`

function ContractManagePart(props) {
    const [modal, setModal] = useState({ status: false, text: '' }); //  state
    const [trialCost, setTrialCost] = useState([]); //  state

    const checkToggle = (value) => { //  checkbox 
        let currentIdx = trialCost.indexOf(value);
        let copy = [...trialCost];
        if (currentIdx === -1) copy.push(value);
        else copy.splice(currentIdx, 1);
        setTrialCost(copy);
    }

    const [clients, setClients] = useState([ //  state
        {clientsName: '', clientsPhone: '', clientsJumin: '', clientsAddress: ''}
    ]); 

    const clientsInfoRowH = (types, index) => { //   / 
        let copy = [...clients];
        if (types === 'plus') {
            copy.push({clientsName: '', clientsPhone: '', clientsJumin: '', clientsAddress: ''});
            setClients(copy);
        } else {
            copy.splice(index, 1);
            setClients(copy);
        }
    };

    const changeClientInfo = (e, index, target) => { //    
        let copy = [...clients];
        if (target === "clientsPhone" || target === "clientsJumin" ) copy[index][target] = parseOnlyPhone(e.target.value);
        else copy[index][target] = e.target.value;
        setClients(copy);
    };

    const [deputys, setDeputys] = useState([ //  state
        {deputysName: '', deputysPhone: '', deputysJumin: '', deputysAddress: '', deputysRelation: ''}
    ]); 

    const deputysInfoRowH = (types, index) => { //   / 
        let copy = [...deputys];
        if (types === 'plus') {
            copy.push({deputysName: '', deputysPhone: '', deputysJumin: '', deputysAddress: '', deputysRelation: ''});
            setDeputys(copy);
        } else {
            copy.splice(index, 1);
            setDeputys(copy);
        }
    };

    const changeDeputysInfo = (e, index, target) => { //    
        let copy = [...deputys];
        if (target === "deputysPhone" || target === "deputysJumin" ) copy[index][target] = parseOnlyPhone(e.target.value);
        else copy[index][target] = e.target.value;
        setDeputys(copy);
    };

    const [otherPay, setOtherPay] = useState([ //  state
        {articles: '', articlesMoney: ''}
    ]); 

    const otherPayRowH = (types, index) => { //   / 
        let copy = [...otherPay];
        if (types === 'plus') {
            copy.push({articles: '', articlesMoney: ''});
            setOtherPay(copy);
        } else {
            copy.splice(index, 1);
            setOtherPay(copy);
        }
    };

    const changeOtherPay = (e, index, target) => { //    
        let copy = [...otherPay];
        if (target === "articlesMoney" ) copy[index][target] = parseCurrency(e.target.value);
        else copy[index][target] = e.target.value;
        setOtherPay(copy);
    };

    const [contractInfo, setContractInfo] = useState({ //   state
        caseBig: '', // ()
        caseMiddle: '', // ()
        opponent: '', // 
        affairInfo1: '', // 
        affairInfo2: '', //  
        affairInfo3: '', //   - ''    
        installmentSchedule: '', // 
        successPay1: '', //  
        successPay2: '', //  
        successVAT: '', // 
        successPay3: '', //  
        etcCon: '', // 
    }); 

    const {
        caseBig,
        caseMiddle,
        opponent,
        affairInfo1,
        affairInfo2,
        affairInfo3,
        installmentSchedule,
        successPay1,
        successPay2,
        successVAT,
        successPay3,
        etcCon
    } = contractInfo;
    
    const contractInputHandler = (e) => { 
        let {name, value} = e.target;

        if (name == 'affairInfo2') {
            if (value == '') contractInfo.affairInfo3 = "";
        } else if (name == 'successPay3') {
            value = parseCurrency(value);
        }

        setContractInfo({
            ...contractInfo,
            [name]: value
        })
    };

    const checkRequired = (classNames) => { //   
        let checkEl = document.querySelectorAll(`.${classNames}`);
        for (let i = 0; i < checkEl.length; i++) {
            if (checkEl[i].value === "" && !checkEl[i].disabled) {
                let copy = {...modal};
                copy.status = true;
                copy.text = `${checkEl[i].getAttribute('data-required')} .`
                setModal(copy);
                return false;
            }
        }
        return true;
    };

    const submitContractInfo = (classNames) => { //   
        if ( checkRequired(classNames) ) {
            let dataToSubmit = Object.assign(contractInfo, {clients: clients}, {deputys: deputys}, {otherPay: otherPay} ); 
            console.log(' ', dataToSubmit);
        }
    };

    const closeModal = () => { setModal(false); }; //   

    const buttonStyle = useMemo(() => ({fontSize: 20, verticalAlign: 'middle'}), []);

    return (
        <div className="common-part mb-20 mt-20">
            <section className="part-title clearfix">
                <p className="title-txt"></p>
                <p className="btn-wrapper">
                    <span 
                        className={ `default-btn-theme ml-5 ${props.PartShowAndHide.contractManage ? 'minus' : 'plus'}` }
                        onClick={()=>{props.partToggle('contractManage')}}>
                    </span>
                </p>
            </section>
            
            {
                props.PartShowAndHide.contractManage
                &&
                    <section className="part-content">

                        {/* contract-info-table */}
                        <table className='table contract-info-table'>
                            <tr>
                                <th rowSpan={7 + clients.length + deputys.length}>
                                    <p></p>    
                                    <button 
                                    onClick={()=>{submitContractInfo('contract_required')}}
                                    className="default-btn-blue big-btn mt-20 mb-20"></button><br/>
                                    <Link target="_blank" to={`/reportAgreementPrint/123`} rel="noopener noreferrer" className="default-btn-theme mb-5"> </Link><br/>
                                    <Link target="_blank" to={`/reportAgreementView/123`} rel="noopener noreferrer" className="default-btn-theme"> </Link>
                                </th>
                                <th><i className="required"></i>()</th>
                                <td>
                                    <select 
                                    data-required="()"
                                    name="caseBig"
                                    value={caseBig}
                                    onChange={contractInputHandler}
                                    className="common-input w-100p contract_required">
                                        <option value=""></option>
                                        {caseName1.map((el,idx)=>(<option key={idx} value={el}>{el}</option>))}
                                    </select>
                                </td>
                                <th><i className="required"></i>()</th>
                                <td>
                                    <select
                                    data-required="()"
                                    name="caseMiddle"
                                    value={caseMiddle}
                                    onChange={contractInputHandler}
                                    className="common-input w-100p contract_required">
                                        <option value=""></option>
                                        {caseName2.map((el,idx)=>(<option key={idx} value={el}>{el}</option>))}
                                    </select>
                                </td>
                                <th></th>
                                <td colSpan="5">
                                    <input type="text" 
                                    name="opponent"
                                    value={opponent}
                                    onChange={contractInputHandler}
                                    className="common-input w-100p"/>
                                </td>
                            </tr>   

                            {
                                clients.map((el, idx) => (
                                    <tr key={idx}>
                                        <th><i className="required"></i> 
                                            {
                                                idx === 0 
                                                ? <button onClick={()=>{clientsInfoRowH('plus', idx)}} className="default-btn-green ml-5"></button>
                                                : <button onClick={()=>{clientsInfoRowH('minus', idx)}} className="default-btn-red ml-5"></button>
                                            }
                                        </th>
                                        <td>
                                            <input type="text" 
                                            data-required=""
                                            value={el.clientsName}
                                            onChange={(e)=>{changeClientInfo(e, idx, "clientsName")}}
                                            className="common-input w-100p contract_required"/>
                                        </td>
                                        <th><i className="required"></i> </th>
                                        <td>
                                            <input type="text" 
                                            data-required=" "
                                            value={el.clientsPhone}
                                            onChange={(e)=>{changeClientInfo(e, idx, "clientsPhone")}}
                                            className="common-input w-100p contract_required"/>
                                        </td>
                                        <th><i className="required"></i> </th>
                                        <td>
                                            <input type="text" 
                                            value={el.clientsJumin}
                                            onChange={(e)=>{changeClientInfo(e, idx, "clientsJumin")}}
                                            data-required=" "
                                            className="common-input w-100p contract_required"/>
                                        </td>
                                        <th><i className="required"></i> </th>
                                        <td colSpan='3'>
                                            <input type="text" 
                                            value={el.clientsAddress}
                                            onChange={(e)=>{changeClientInfo(e, idx, "clientsAddress")}}
                                            data-required=" "
                                            className="common-input w-100p contract_required"/>
                                        </td>
                                    </tr>     
                                ))
                            }

                            {
                                deputys.map((el, idx) => (
                                    <tr key={idx}>
                                        <th>
                                            <i className="required"></i> 
                                            {
                                                idx === 0 
                                                ? <button onClick={()=>{deputysInfoRowH('plus', idx)}} className="default-btn-green ml-5"></button>
                                                : <button onClick={()=>{deputysInfoRowH('minus', idx)}} className="default-btn-red ml-5"></button>
                                            }
                                        </th>
                                        <td>
                                            <input type="text" 
                                            data-required=""
                                            value={el.deputysName}
                                            onChange={(e)=>{changeDeputysInfo(e, idx, "deputysName")}}
                                            className="common-input w-100p contract_required"/>
                                        </td>
                                        <th><i className="required"></i> </th>
                                        <td>
                                            <input type="text" 
                                            data-required=" "
                                            value={el.deputysPhone}
                                            onChange={(e)=>{changeDeputysInfo(e, idx, "deputysPhone")}}
                                            className="common-input w-100p contract_required"/>
                                        </td>
                                        <th><i className="required"></i> </th>
                                        <td>
                                            <input type="text" 
                                            data-required=" "
                                            value={el.deputysJumin}
                                            onChange={(e)=>{changeDeputysInfo(e, idx, "deputysJumin")}}
                                            className="common-input w-100p contract_required"/>
                                        </td>
                                        <th><i className="required"></i> </th>
                                        <td>
                                            <input type="text" 
                                            data-required=" "
                                            value={el.deputysAddress}
                                            onChange={(e)=>{changeDeputysInfo(e, idx, "deputysAddress")}}
                                            className="common-input w-100p contract_required"/>
                                        </td>
                                        <th> </th>
                                        <td>
                                            <input type="text" 
                                            value={el.deputysRelation}
                                            onChange={(e)=>{changeDeputysInfo(e, idx, "deputysRelation")}}
                                            className="common-input w-100p"/>
                                        </td>
                                    </tr>   
                                ))
                            }
                            <tr>
                                <th><i className='required'></i></th>
                                <td colSpan="4">
                                    <textarea 
                                    data-required=""
                                    name="affairInfo1"
                                    value={affairInfo1}
                                    onChange={contractInputHandler}
                                    className='common-input w-100p contract_required'></textarea>
                                </td>
                                <th><i className="required"></i> </th>
                                <td colSpan="4" className='ta-l'>
                                    <span className='units-wrap'>
                                        <input 
                                            type="radio" 
                                            name='affairInfo2' 
                                            value='' 
                                            id="affairInfo2_1"
                                            onChange={contractInputHandler} 
                                            checked={affairInfo2 === ""}
                                        />
                                        <label htmlFor="affairInfo2_1" className="ml-5 mr-10"></label>
                                        <input 
                                            type="radio" 
                                            name='affairInfo2' 
                                            value='' 
                                            id="affairInfo2_2"
                                            onChange={contractInputHandler} 
                                            checked={affairInfo2 === ""}
                                        />
                                        <label htmlFor="affairInfo2_2" className="ml-5 mr-10"></label>
                                        <input 
                                        type="text" 
                                        disabled={affairInfo2 === ""}
                                        name='affairInfo3' 
                                        value={affairInfo3} 
                                        onChange={contractInputHandler} 
                                        data-required="  "
                                        className="common-input contract_required"/>
                                    </span>
                                </td>
                            </tr>
                            <tr>
                                <th></th>
                                <td colSpan="9">
                                    <textarea 
                                    name='installmentSchedule' 
                                    value={installmentSchedule} 
                                    onChange={contractInputHandler} 
                                    className='common-input w-100p'></textarea>
                                </td>
                            </tr>
                            <tr>
                                <th><i className="required"></i> /</th>
                                <td>
                                    <select 
                                    data-required=" /"
                                    name='successPay1' 
                                    value={successPay1} 
                                    onChange={contractInputHandler} 
                                    className="common-input w-100p contract_required">
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                    </select>
                                </td>
                                <th><i className="required"></i> </th>
                                <td colSpan='2'>
                                    <textarea 
                                    data-required=" "
                                    name='successPay2' 
                                    value={successPay2} 
                                    onChange={contractInputHandler} 
                                    className='common-input w-100p contract_required'></textarea>
                                </td>
                                <th><i className="required"></i></th>
                                <td>
                                    <select 
                                    name='successVAT' 
                                    data-required=""
                                    value={successVAT} 
                                    onChange={contractInputHandler} 
                                    className="common-input w-100p contract_required">
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                    </select>
                                </td>
                                <th><i className="required"></i> </th>
                                <td colSpan="2" className="ta-l">
                                    <input type="text" 
                                    name='successPay3' 
                                    value={successPay3} 
                                    onChange={contractInputHandler} 
                                    data-required=" "
                                    className="common-input mr-5 contract_required"/>
                                </td>
                            </tr>
                            <tr>
                                <th><i className="required"></i></th>
                                <td colSpan="9" className='ta-l'>
                                    <section className="clearfix">
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('') === -1 ? false : true}
                                            name="trialCost" value="" id="trialCost_1" onChange={()=>{checkToggle('')}}/>
                                            <label htmlFor="trialCost_1"></label>
                                        </CheckLists>
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('') === -1 ? false : true}
                                            name="trialCost" value="" id="trialCost_2" onChange={()=>{checkToggle("")}}/>
                                            <label htmlFor="trialCost_2"></label>
                                        </CheckLists>
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('') === -1 ? false : true}
                                            name="trialCost" value="" id="trialCost_3" onChange={()=>{checkToggle("")}}/>
                                            <label htmlFor="trialCost_3"></label>
                                        </CheckLists>
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('') === -1 ? false : true}
                                            name="trialCost" value="" id="trialCost_4" onChange={()=>{checkToggle("")}}/>
                                            <label htmlFor="trialCost_4"></label>
                                        </CheckLists>
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('') === -1 ? false : true}
                                            name="trialCost" value="" id="trialCost_5" onChange={()=>{checkToggle("")}}/>
                                            <label htmlFor="trialCost_5"></label>
                                        </CheckLists>
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('') === -1 ? false : true}
                                            name="trialCost" value="" id="trialCost_6" onChange={()=>{checkToggle("")}}/>
                                            <label htmlFor="trialCost_6"></label>
                                        </CheckLists>
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('') === -1 ? false : true}
                                            name="trialCost" value="" id="trialCost_7" onChange={()=>{checkToggle("")}}/>
                                            <label htmlFor="trialCost_7"></label>
                                        </CheckLists>
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('') === -1 ? false : true}
                                            name="trialCost" value="" id="trialCost_8" onChange={()=>{checkToggle("")}}/>
                                            <label htmlFor="trialCost_8"></label>
                                        </CheckLists>
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('') === -1 ? false : true}
                                            name="trialCost" value="" id="trialCost_9" onChange={()=>{checkToggle("")}}/>
                                            <label htmlFor="trialCost_9"></label>
                                        </CheckLists>
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('') === -1 ? false : true}
                                            name="trialCost" value="" id="trialCost_10" onChange={()=>{checkToggle("")}}/>
                                            <label htmlFor="trialCost_10"></label>
                                        </CheckLists>
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('') === -1 ? false : true}
                                            name="trialCost" value="" id="trialCost_11" onChange={()=>{checkToggle("")}}/>
                                            <label htmlFor="trialCost_11"></label>
                                        </CheckLists>
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('') === -1 ? false : true}
                                            name="trialCost" value="" id="trialCost_12" onChange={()=>{checkToggle("")}}/>
                                            <label htmlFor="trialCost_12"></label>
                                        </CheckLists>
                                        <CheckLists>
                                            <input type="checkbox" 
                                            checked={trialCost.indexOf('') === -1 ? false : true}
                                            name="trialCost" value="" id="trialCost_13" onChange={()=>{checkToggle("")}}/>
                                            <label htmlFor="trialCost_13"></label>
                                        </CheckLists>
                                    </section>
                                </td>
                            </tr>
                            <tr>
                                <th></th>
                                <td colSpan="9">
                                    <table className='table'>
                                        {
                                            otherPay.map((el, idx) => (
                                                <tr key={idx}>
                                                    <th>
                                                        {
                                                            idx === 0
                                                            ?   <button style={buttonStyle} 
                                                                onClick={()=>{otherPayRowH('plus', idx)}}
                                                                className="default-btn-green mr-5">+</button>
                                                            :   <button style={buttonStyle} 
                                                                onClick={()=>{otherPayRowH('minus', idx)}}
                                                                className="default-btn-red mr-5">-</button>
                                                            }
                                                        () 
                                                    </th>
                                                    <td><input 
                                                    type="text" 
                                                    value={el.articles}
                                                    onChange={(e)=>{changeOtherPay(e, idx, "articles")}}
                                                    className="common-input mr-5"/></td>
                                                    <th>() </th>
                                                    <td><input 
                                                    type="text" 
                                                    value={el.articlesMoney}
                                                    onChange={(e)=>{changeOtherPay(e, idx, "articlesMoney")}}
                                                    className="common-input mr-5"/></td>
                                                </tr>
                                            ))
                                        }
                                    </table>
                                </td>
                            </tr>
                            <tr>
                                <th></th>
                                <td colSpan="9">
                                    <input type="text" 
                                    name="etcCon"
                                    value={etcCon}
                                    onChange={contractInputHandler} 
                                    className="common-input w-100p"/>
                                </td>
                            </tr>
                            <tr>
                                <th></th>
                                <td colSpan="10" className="ta-l">
                                    <button className="default-btn-red mr-5"></button>
                                    <button className="default-btn-green" onClick={()=>{
                                        document.querySelector('#agreement-upload').click();
                                    }}></button>
                                    <input type="file" className="ir_so" id="agreement-upload"/>
                                </td>
                            </tr>
                        </table>
                        {/* // contract-info-table */}

                        {/*   */}
                        <LegalReportTable />
                        {/* //   */}
                    </section>
            }
            {modal.status && <Modal refreshFunction={closeModal} modalText={modal.text}/>}
        </div>
    )
}

export default ContractManagePart
