import React, { useState } from 'react'
import './Sections/MemberInfoPage.css';
import {departments1, departments2, departments3} from '../../../Datas/datas';
import { Link } from 'react-router-dom';

function MemberInfoPage(props) {
    const memberId = props.match.params.memberId;
    const [userId, setUserId] = useState('');
    const [PassWord, setPassWord] = useState('');
    const [PassWordCheck, setPassWordCheck] = useState('');
    const [Name, setName] = useState('');
    
    const [Departments1, setDepartments1] = useState('');
    const [Departments2, setDepartments2] = useState('');
    const [Departments3, setDepartments3] = useState('');
    const [center, setCenter] = useState('');
    const [permission, setPermission] = useState('');
    const [role, setRole] = useState('');
    const [isLeave, setIsLeave] = useState(false);

    const depart1Handler = (e) => { 
        setDepartments1(e.target.value);
        setDepartments2('');
    }
    const depart2Handler = (e) => { 
        setDepartments2(e.target.value);
        setDepartments3('');
    }
    const depart3Handler = (e) => { setDepartments3(e.target.value); }

    const addNewMember = () => { //    
        let dataToSubmit = {
            userId: userId,
            userPassword: PassWord,
            userName: Name,
            department1: Departments1,
            department2: Departments2,
            department3: Departments3,
            center: center,
            permission: permission,
            role: role,
            isLeave: isLeave
        };
        console.log('  ', dataToSubmit);
    };

    const updateMember = () => { //    

    };

    return (
        <div className="page-container member-info-page">

            <div className="table-title clearfix mt-0">
                <p className="title-txt">{memberId === "addMember" ? " " : " "}</p>
            </div>  
        
            <div className="mt-30">
                <table className="table member_table mb-10">
                    <tbody>
                        <tr>
                            <th className="w-250"></th>
                            {
                                memberId === "addMember"
                                ? (
                                    <td><input className="common-input w-300" type="text" value={userId} onChange={(e)=>{setUserId(e.target.value)}}/></td>
                                )   
                                : <td>{userId}</td>
                            }
                        </tr>
                        <tr>
                            <th></th>
                            <td>
                                <input className="common-input w-300" type="password" value={PassWord} onChange={(e)=>{setPassWord(e.target.value)}}/>
                                <span className="ml-10"> 4~20 ,  </span>    
                            </td>
                        </tr>
                        <tr>
                            <th> </th>
                            <td><input className="common-input w-300" type="password" value={PassWordCheck} onChange={(e)=>{setPassWordCheck(e.target.value)}}/></td>
                        </tr>
                        <tr>
                            <th></th>
                            <td><input className="common-input w-300" type="text" value={Name} onChange={(e)=>{setName(e.target.value)}}/></td>
                        </tr>
                        <tr>
                            <th></th>
                            <td>
                                <select className="common-input mr-5 department-select-1 w-150" onChange={depart1Handler} value={Departments1}>
                                    <option value="">()</option>
                                    {departments1.map((el, idx) => (
                                        <option key={idx} value={el.keys}>{el.name}</option>
                                    ))}
                                </select>
                                <select className="common-input mr-5 department-select-2 w-150" onChange={depart2Handler} value={Departments2}>
                                    <option value="">()</option>
                                    {
                                        Departments1 != "" &&
                                        departments2[Departments1].map((el, idx) => (
                                            <option key={idx} value={el.keys}>{el.name}</option>
                                        ))
                                    }
                                </select>
                                <select className="common-input mr5 department-select-3 w-150" onChange={depart3Handler} value={Departments3}>
                                    <option value="">()</option>
                                    {
                                        Departments2 != "" &&
                                        departments3[Departments2].map((el, idx) => (
                                            <option key={idx} value={el}>{el}</option>
                                        ))
                                    }
                                </select>
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td>
                                <select className="common-input w-150" value={center} onChange={(e)=>{setCenter(e.target.value)}}>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                </select>
                            </td>
                        </tr>
                        <tr>
                            <th> </th>
                            <td>
                                <select className="common-input w-150" value={permission} onChange={(e)=>{setPermission(e.target.value)}}>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value="1">1</option>
                                    <option value="2">2</option>
                                    <option value=""></option>
                                    <option value=""></option>
                                </select>
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td>
                                <select className="common-input w-150" value={role} onChange={(e)=>{setRole(e.target.value)}}>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                </select>
                            </td>
                        </tr>
                        <tr>
                            <th> </th>
                            <td>
                                <input 
                                type="checkbox"
                                id="no-more-work" 
                                className="mr-5"
                                checked={isLeave}
                                onChange={()=>{setIsLeave(!isLeave)}}
                                />
                                <label htmlFor="no-more-work">(  )</label>
                            </td>
                        </tr>
                    </tbody>
                </table>
                {
                    memberId === "addMember"
                    ? <button onClick={addNewMember} className="default-btn-theme mr-5"></button>
                    : <button onClick={updateMember} className="default-btn-theme mr-5"></button>
                }
                <Link className="default-btn" to="/memberList"></Link>
            </div>
        </div>
    )
}

export default MemberInfoPage
